import {CONSTANTS} from "../sn-wsd-moa-app/constants";

/**
 * Object to hold the instantiated officejs singleton
 * @type {{officeHelper: null}}
 */
const MOAData = {
	officeHelper: null,
	appointment: null,
	ewsUrl: null
};


class OfficeHelper {
	constructor(Office) {
		this.Office = Office;
		this.mailboxItem = this.Office.context.mailbox.item;
		this.appointment = null;
		this.ewsUrl = null;
		this.ewsId = null;
	}

	SAVE_TEXT_FORMAT = ' ';
	SAVE_TEXT_REGEX = new RegExp(' $', 'gm');

	/**
	 * Checks which versions of officeJs are supported based on the versions array in the constants
	 * @return {Object.<string, boolean>} - dictionary of version: string and boolean on whether the current client supports this version
	 */
	supportedVersions() {
		const platformType = CONSTANTS.OUTLOOK_PLATFORM_TYPE;
		const versions = CONSTANTS.SUPPORTED_VERIONS;
		return versions.reduce((total, current) => {
			total[current] = this.Office.context.requirements.isSetSupported(platformType, current);
			return total;
		}, {});
	}

	/**
	 * Retrieves meeting data from officejs
	 * @return {Promise<MeetingData>}
	 */
	async getMeetingData() {
		const fields = ['itemType', 'subject', 'organizer', 'location', 'requiredAttendees', 'optionalAttendees'];
		const values = await this.getItemData(...fields);
		const fieldData = fields.reduce((total, current, index) => {
			total[current] = values[index];
			return total;
		}, {});

		const recurrenceInfo = await this.getRecurringData();
		return {
			id: await this.getItemId().catch(() => null),
			start: await this.getStart(),
			end: await this.getEnd(),
			recurrenceInfo: recurrenceInfo,
			...fieldData
		}
	}

	/**
	 * Gets information about the frequency and types of recurrence from a meeting
	 * @return {Promise<{[recurrence]: Recurrence, type: string, [seriesId]: string}>}
	 */
	async getRecurringData() {
		const recurrence = await this.getItemData('recurrence').catch(() => null);
		const seriesId = await this.getItemData('seriesId').catch(() => null)

		return {
			recurrence: recurrence,
			seriesId: seriesId,
			type: this.calculateAppointmentType(recurrence, seriesId)
		};
	}

	/**
	 * Depending on the value of recurrence and seriesId, the reservation type will be calculated
	 * @param {object} recurrence 
	 * @param {string} seriesId 
	 * @returns {{recurrence: object, seriesId: object, type}}
	 */
	 calculateAppointmentType(recurrence, seriesId) {
		let recurringType;

		if (seriesId)
			recurringType = CONSTANTS.RECURRENCE_TYPES.OCCURRENCE;
		else if (recurrence)
			recurringType = CONSTANTS.RECURRENCE_TYPES.MASTER;
		else
			recurringType = CONSTANTS.RECURRENCE_TYPES.SINGLE;

		return recurringType;
	}

	/**
	 * funtionality that is called when the addin is first started, to make sure the appointment can be loaded etc.
	 * if the appointment can't be found or saved. application will be terminated
	 * @returns {Promise<void>}
	 */
	async initializeAppointment() {
		const base = this;
		try {			
			return base.prepareAppointment().then((result) => {
				console.log("Appointment prepared. Result: ", result);
				base.ewsId = result;
			});
		} catch (ex) {
			console.error('Failed to process custom properties. This might due to unsupported client or officeHelper malfunction', ex);
			return reject(ex);
		}
	}

	/**
	 * Prepare appointment using custom properites. Save appointment (with default subject) if not previously saved.
	 * Store EwsId to custom properties, to avoid resaving appointment and cause annoying popup in outlook
	 * @returns {Promise<unknown>}
	 */
	async prepareAppointment() {
		var base = this;
		return new Promise((resolve, reject) => {
			this.mailboxItem.loadCustomPropertiesAsync(async (asyncResult) => {
				if (asyncResult.status === this.Office.AsyncResultStatus.Failed) {
					reject(asyncResult.error);
					return;
				}

				const customProps = asyncResult.value;
				let ewsId = customProps.get("ewsId");

				if (ewsId) {
					// appointment was previously loaded and saved
					console.log('EwsId existed, appointment was previous saved');
					resolve(ewsId);
					return;
				}

				// complete new appointment from add-in perspective
				ewsId = await base.saveAppointmentAsync();
				customProps.set("ewsId", ewsId);
				console.debug('Saving EwsId to custom properties...');
				customProps.saveAsync();
				resolve(ewsId);
			});
		})
	}

	/**
	 * generate a subject for a appointment based on current subject
	 * @param currentSubject
	 * @returns {string}
	 */
	generateSubject(currentSubject) {
		let newSubject;
		if (!currentSubject || currentSubject === '') {
			newSubject = CONSTANTS.DEFAULT_SUBJECT;
		} else if (currentSubject.match(this.SAVE_TEXT_REGEX) !== null) {
			newSubject = currentSubject.replace(this.SAVE_TEXT_REGEX, '');
		} else {
			newSubject = currentSubject + this.SAVE_TEXT_FORMAT;
		}
		return newSubject

	}

	/**
	 * force a change in the appointment so that it can be saved towards exchange, used when the addin is loaded initially
	 * @param customProps
	 * @returns {Promise<void>}
	 */
	async saveAppointmentAsync(...customProps) {
		const base = this;

		return base.getSubject().then(async (currentAppointmentSubject) => {
			const newSubject = base.generateSubject(currentAppointmentSubject);

			await base.setItemData('subject', newSubject);
			
			return base.saveMailboxItemAsync();
		});
	}

	/**
	 * Calls mailboxItem save async, forcing the appointment to be saved in exchange
	 * @returns {Promise<string>} (contain ewsId value)
	 */
	async saveMailboxItemAsync() {
		return new Promise((resolve, reject) => {
			this.mailboxItem.saveAsync((asyncResult) => {
				if (asyncResult.status === this.Office.AsyncResultStatus.Failed)
					reject(asyncResult.error);
				else
					resolve(asyncResult.value);

			})
		})
	}

	/**
	 * Gets the subject of the open meeting in the outlook frame
	 * @return {Promise<string>}
	 */
	async getSubject() {
		return this.getItemData('subject');
	}

	/**
	 * Sets the subject of the open meeting in the outlook frame
	 * @param subject
	 * @return {Promise<string>}
	 */
	async setSubject(subject) {
		return this.setItemData('subject', subject);
	}

	/**
	 * gets the start datetime of the meeting
	 * @return {Promise<Date>}
	 */
	async getStart() {
		return new Date(await this.getItemData('start'))
	}

	/**
	 * set the start of the meeting
	 * @param {Date} start
	 * @return {Promise<string>}
	 */
	async setStart(start) {
		return this.setItemData('start', start);
	}

	/**
	 * gets the end datetime of the meeting
	 * @return {Promise<Date>}
	 */
	async getEnd() {
		return new Date(await this.getItemData('end'));
	}

	/**
	 * sets the end datetime of the meeting
	 * @param {Date} end
	 * @return {Promise<string>}
	 */
	async setEnd(end) {
		return this.setItemData('end', end);
	}

	/**
	 * Getter for required attendees field.
	 * @return {Promise<EmailAddressDetails[]>}
	 */
	async getRequiredAttendees() {
		return this.getItemData('requiredAttendees');
	}

	/**
	 * Setter for required attendees field.
	 * @param {EmailAddressDetails[]} requiredAttendees - The attendees to set to required attendees field.
	 */
	async setRequiredAttendees(requiredAttendees) {
		await this.setItemData('requiredAttendees', requiredAttendees);
	}

	/**
	 * Adds provided email address details to the required attendees.
	 * @param {EmailAddressDetails} newAttendee - The attendee to add to required attendees.
	 */
	async addRequiredAttendee(newAttendee) {
		let requiredAttendees = await this.getRequiredAttendees();

		if (requiredAttendees.some((attendee) => attendee.emailAddress === newAttendee.emailAddress))
			return;

		requiredAttendees.push(newAttendee);
		await this.setRequiredAttendees(requiredAttendees);
	}

	/**
	 * Removes a attendee from the required attendees list.
	 * @param {string} attendeeEmail - SMTP email address of the attendee to remove.
	 */
	async removeRequiredAttendee(attendeeEmail) {
		let requiredAttendees = await this.getRequiredAttendees();
		let attendeeIndex = requiredAttendees.findIndex((attendee) => attendee.emailAddress === attendeeEmail);

		if (attendeeIndex > -1) {
			requiredAttendees.splice(attendeeIndex, 1);
			await this.setRequiredAttendees(requiredAttendees)
		}
	}

	/**
	 * Adds an enhanced location to the meeting.
	 * @param {string} roomEmail - SMTP Email address of the room to add.
	 */
	async addEnhancedLocation (roomEmail) {
		if(!roomEmail)
			return;

		let enhancedLoc = [{
			id: roomEmail,
			type: this.Office.MailboxEnums.LocationType.Room
		}];

		this.mailboxItem['enhancedLocation'].addAsync(enhancedLoc);
	}

	/**
	 * Removes an enhanced location to the meeting.
	 * @param {string} roomEmail - SMTP Email address of the room to remove.
	 */
	async removeEnhancedLocation (roomEmail) {
		if(!roomEmail)
			return;

		let enhancedLoc = [{
			id: roomEmail,
			type: this.Office.MailboxEnums.LocationType.Room
		}];

		this.mailboxItem['enhancedLocation'].removeAsync(enhancedLoc);
	}

	/**
	 * Adds a room to the meeting.
	 * @param {string} roomName - Display name of the room.
	 * @param {string} roomEmail - SMTP email address of the room.
	 */
	async addRoomToMeeting (roomName, roomEmail) {
		let supportedVersions = this.supportedVersions();

		if (supportedVersions['1.8']) {
			await this.addEnhancedLocation(roomEmail);
		} else {
			if(!roomName || !roomEmail)
				return;

			let requiredAttendee = {
				emailAddress: roomEmail,
				displayName: roomName,
				recipientType: 'other'
			};

			await this.addRequiredAttendee(requiredAttendee);
		}
	}

	/**
	 * Removes a room from the meeting.
	 * @param {string} roomName - Display name of the room.
	 * @param {string} roomEmail - SMTP email address of the room.
	 */
	async removeRoomFromMeeting (roomName, roomEmail) {
		let supportedVersions = this.supportedVersions();

		if (supportedVersions['1.8']) {
			await this.removeEnhancedLocation(roomEmail);
		} else {
			await this.removeRequiredAttendee(roomEmail);
		}
	}

	/**
	 * Wrapper function around getting data from the officejs API
	 * @param {...string} fields - "rest" operator array of all fields that are passed in to retrieve
	 * @return {Promise<string|string[]>}
	 * @example
	 * await office.getItemDate('subject');
	 * // returns 'meeting subject'
	 * await office.getItemDate('subject', 'location');
	 * // returns ['meeting subject', 'Tue Mar 23 2021 13:45:06 GMT+0100 (Central European Standard Time)']
	 */
	async getItemData(...fields) {
		if (fields.length === 0)
			throw Error('Invalid arguments count, cannot get 0 fields');

		if (fields.length === 1)
			return this._getItemDataSingle(fields[0]);

		const promises = fields.map((field) => this._getItemDataSingle(field));

		return Promise.all(promises);
	}

	/**
	 * Documentation on which fields are available can be found here
	 * https://docs.microsoft.com/en-us/office/dev/add-ins/reference/objectmodel/requirement-set-1.7/office.context.mailbox.item#methods
	 * @param {string} field
	 * @return {Promise<string>}
	 * @private
	 */
	async _getItemDataSingle(field) {
		return new Promise((resolve, reject) => {
			try {
				if (!field)
					reject(Error('Invalid parameter'));
	
				if (!this.mailboxItem[field]) {
					console.error(`Invalid field on mailbox item: ${field}`);
					resolve(null);
				}

				// check for fields that are not retrieved with getAsync but need to be called directly
				if (!this.mailboxItem[field].getAsync || typeof (this.mailboxItem[field].getAsync) !== 'function')
					resolve(this.mailboxItem[field]);

				this.mailboxItem[field].getAsync(
					(asyncResult) => {
						if (asyncResult.status === this.Office.AsyncResultStatus.Failed) {
							console.error(`Get async result in failed status: ${field}, ${asyncResult.error.message}`);
							resolve(null);
						}
						else
							resolve(asyncResult.value);
					}
				);
			} catch(ex) {
				console.error('Unable to get outlook item data', field);
				return Promise.resolve(null);
			}
		});
	}

	/**
	 * Promise wrapper around getting the id
	 * @return {Promise<string>}
	 */
	async getItemId() {
		return new Promise((resolve, reject) => {
			this.mailboxItem.getItemIdAsync((asyncResult) => {
				if (asyncResult.status === this.Office.AsyncResultStatus.Failed)
					reject(asyncResult.error.message);
				else
					resolve(asyncResult.value);
			})
		})
	}

	/**
	 * wrapper function around officejs that will set data in the outlook frame
	 * @param {string} field
	 * @param value
	 * @return {Promise<string>} return is based on what field type you set
	 */
	async setItemData(field, value) {
		return new Promise((resolve, reject) => {

			if (!field)
				reject(Error('Invalid parameter'));

			const options = {};
			this.mailboxItem[field].setAsync(value, options,
				(asyncResult) => {
					if (asyncResult.status === this.Office.AsyncResultStatus.Failed)
						reject(asyncResult.error.message);
					else
						resolve(asyncResult.value);
				});
		});
	}

	/**
	 * notifies subscribers of changes in the outlook window for the time data
	 * @param {changeSubscriberHandlerResult} handler
	 */
	subscribeTimeChanges(handler) {
		this._subscribeChangeHandler(this.Office.EventType.AppointmentTimeChanged, (result) => {
			handler(result);
		});
	}

	/**
	 * notifies subscribers of changes in the outlook window for the location data
	 * @param {changeSubscriberHandlerResult} handler
	 */
	subscribeEnhancedLocationChanges(handler) {
		this._subscribeChangeHandler(this.Office.EventType.EnhancedLocationsChanged, (result) => {
			handler(result);
		});
	}

	/**
	 * notifies subscribers of changes in the outlook window for the recipients data
	 * @param {changeSubscriberHandlerResult} handler
	 */
	subscribeRecipientsChanges(handler) {
		this._subscribeChangeHandler(this.Office.EventType.RecipientsChanged, async (result) => {
			result.data = result.data.changedRecipientFields;
			handler(result);
		});
	}

	/**
	 * notifies subscribers of changes in the outlook window for the recurrence data
	 * @param {changeSubscriberHandlerResult} handler
	 */
	subscribeRecurrenceChanged(handler) {
		this._subscribeChangeHandler(this.Office.EventType.RecurrenceChanged, async (result) => {
			handler(result);
		});
	}

	/**
	 * helper to wrap around creating a subscriber on changes in the outlook window
	 * @param {string} eventType - one of the options from Office.EventType
	 * @param {changeSubscriberHandler} handler
	 * @private
	 */
	_subscribeChangeHandler(eventType, handler) {
		if (!handler)
			throw Error('Invalid arguments, missing param handler');

		this.Office.context.mailbox.item.addHandlerAsync(eventType, (asyncResult) => {
			const result = {};
			if (asyncResult.status === this.Office.AsyncResultStatus.Failed) {
				result.status = 'failure';
				result.data = asyncResult.error.message;
			} else {
				result.status = 'success';
				result.data = asyncResult.value || asyncResult;
				// in some cases such as for 'changedRecipientFields', the result is not returned in the value field
			}

			handler(result);
		})
	}

	/**
	 * Closes the addin sidebar
	 */
	closeAddin() {
		window.Office.context.ui.closeContainer();
	}

}

/**
 * @callback changeSubscriberHandler
 * @param {changeSubscriberHandlerResult} result
 */

/**
 * @typedef changeSubscriberHandlerResult
 * @property {'success' | 'failure'} status
 * @property {*} data - data passed to handler with relevant subscriber data
 */

/**
 * Gets the current instance of officeHelper
 * @return {OfficeHelper}
 */
export function getOfficeHelper() {
	if (!MOAData.officeHelper)
		throw Error('Officehelper not available wait for officejs to load');

	return MOAData.officeHelper;
}

/**
 * Initialize officejs either from cdn or from mock with testing.
 * @param {Office} Office - office js library
 */
export function initializeOfficeHelper(Office) {
	if (!MOAData.officeHelper)
		MOAData.officeHelper = new OfficeHelper(Office);
}

/**
 * @typedef MeetingData
 * @property {Date} start
 * @property {Date} end
 * @property {string} [id] - can be empty for non saved meetings
 * @property {object} recurrenceInfo
 * @property {Recurrence} [recurrenceInfo.recurrence] - is empty for single meetings
 * @property {'single' | 'master' | 'occurrence'} recurrenceInfo.type - See CONSTANTS.RECURRENCE_TYPES
 * @property {string} [recurrenceInfo.seriesId] - can be empty for non saved meetings and recurring master meetings and single meetings
 */

/**
 * @typedef Recurrence
 * @property {object} recurrenceProperties
 * @property {number} recurrenceProperties.interval
 * @property {object} recurrenceTimeZone
 * @property {string} recurrenceTimeZone.name
 * @property {number} recurrenceTimeZone.offset
 * @property {string} recurrenceType
 * @property {SeriesTime} seriesTime
 */

/**
 * Recurrence seriesTime object returned by officejs recurrence data
 * @typedef SeriesTime
 * @property {number} durationMinutes
 * @property {number} endDay
 * @property {number} endMont
 * @property {number} endYear
 * @property {number} startDay
 * @property {number} startMonth
 * @property {number} startTimeMinutes
 * @property {number} startYear
 */

/**
 * @typedef EmailAddressDetails
 * @property {string} displayName - The display name associated with an email address
 * @property {string} emailAddress - The SMTP email address
 * @property {string} recipientType - Type of recipient, possible options; distributionList, externalUser, other, user
 */
