import '../sn-wsd-moa-search-filter';
import '../sn-wsd-moa-search-result';

export const view = (state) => {
	const properties = state.properties;
	if (properties.showFilter) {
		const { searchRequest, appointment } = properties;
		return (
			<sn-wsd-moa-search-filter
				searchRequest={searchRequest}
				appointment={appointment}
			/>
		)
	} else {
		const { searchHasMore, hasInitSearched } = state;
		const { searchRequest, reservables, appointment } = properties;
		return (
			<sn-wsd-moa-search-result
				searchRequest={searchRequest}
				reservables={reservables}
				appointment={appointment}
				hasInitSearched={hasInitSearched}
				searchHasMore={searchHasMore}
			/>
		);
	}
}
