import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import  _isEmpty from 'lodash/isEmpty';
import Constants from '../constants';
import { CONSTANTS as APP_CONSTANTS } from '../sn-wsd-moa-app/constants';
import { CONSTANTS as RESERVATION_SUMMARY_CONSTANTS } from './constants';
import { ACTIONS } from './actions';
import { getOfficeHelper } from '../office/officeHelper';
import { insertReservation, updateReservation, insertMultiReservation, updateMultiReservation } from './effects/reservationEffects';
import { submitExtraServicesRequest } from './effects/extraServiceRequestEffects';
import MultiEffectStateWrapper from '../utils/multiEffectStateWrapper';
import * as utils from '../utils/generalUtils';
import { formatUTCDateTimeForUser } from '../utils/moaDateTimeUtils';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED, COMPONENT_DOM_READY} = actionTypes;

const multiEffectStateWrapper = new MultiEffectStateWrapper({
	isFailedKey: 'saveServicesFailed',
	completedCountKey: 'serviceRequestsCompletedCount',
	totalCountKey: 'serviceRequestsSentCount',
	successAction: ACTIONS.ALL_SUBMIT_SERVICE_REQUEST_SUCCESS,
	failureAction: ACTIONS.ALL_SUBMIT_SERVICE_REQUEST_FAILURE
});

export const actionHandlers = {
	[COMPONENT_DOM_READY]: ({action}) => {
		const {payload: {host}} = action;
		const rsvSummary = host.shadowRoot.querySelector('.sn-wsd-moa-reservation-summary');
		
		if (rsvSummary)
			rsvSummary.focus();
	},

	[COMPONENT_BOOTSTRAPPED]: ({ properties, dispatch, state, updateState }) => {
		var { locationsDetails, selectedReservables, appointment } = properties;

		if (_checkAndMoveToSearchResultView(selectedReservables, state.activeView, dispatch))
			return;

		if (_isEmpty(locationsDetails))
			dispatch(ACTIONS.TRIGGER_LOAD_LOCATIONS_DETAILS);

		const isEligibleForSubmission = _checkSubmissionEligibility(selectedReservables, appointment.type);
		updateState({
			...state,
			isEligibleForSubmission
		});
	},

	[COMPONENT_PROPERTY_CHANGED]: ({action: {payload}, properties, state, updateState, dispatch}) => {
		var { name, value } = payload;

		if (name !== 'selectedReservables')
			return;

		const selectedReservables = value;
		if (_checkAndMoveToSearchResultView(selectedReservables, state.activeView, dispatch))
			return;

		const isEligibleForSubmission = _checkSubmissionEligibility(selectedReservables, properties.appointment.type);

		updateState({
			...state,
			isEligibleForSubmission
		});
	},	

	[ACTIONS.INSERT_RESERVATION]: insertReservation,

	[ACTIONS.UPDATE_RESERVATION]: updateReservation,
	
	[ACTIONS.INSERT_MULTI_RESERVATION]: insertMultiReservation,

	[ACTIONS.UPDATE_MULTI_RESERVATION]: updateMultiReservation,

	[ACTIONS.SUBMIT_EXTRA_SERVICES_REQUEST]: submitExtraServicesRequest,
	
	[ACTIONS.SAVE_RESERVATION]: async (...actionInputs) => {
		const [{ dispatch, state, updateState, properties }] = actionInputs;
		const { selectedReservables, appointment } = properties;

		const isEligibleForSubmission = _checkSubmissionEligibility(selectedReservables, appointment.type);;
		if (!isEligibleForSubmission) {
			console.error('One of the items is unavailable, submission blocked');
			return;
		}

		const reservationObj = await createReservationObject(...actionInputs);
		const reservation = properties.reservation;
		const isUpdate = !!reservation;
		const reservationId = isUpdate && reservation.sys_id;
		const reservationIsMulti = isUpdate && !!reservation.locations;
		const isMulti = reservationObj.reservables && reservationObj.reservables.length > 0;
		const requestObj = {
			id: reservationId,
			data: reservationObj
		};

		if (isMulti || reservationIsMulti) {
			if (isUpdate) 
				dispatch(ACTIONS.UPDATE_MULTI_RESERVATION, requestObj);
			else
				dispatch(ACTIONS.INSERT_MULTI_RESERVATION, requestObj);
		} else {
			if (isUpdate) 
				dispatch(ACTIONS.UPDATE_RESERVATION, requestObj);
			else
				dispatch(ACTIONS.INSERT_RESERVATION, requestObj);
		}

		let newState = {
			...state,
			activeView: RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_IN_PROGRESS,
			savedReservables: selectedReservables.filter(selectedReservable => selectedReservable.existsInWsd)
		};

		newState = multiEffectStateWrapper.reset(newState);
		updateState(newState);
	},
	
	[ACTIONS.SAVE_RESERVATION_SUCCESS]: ({ dispatch, action, state, properties, updateState }) => {
		const {
			sys_id: reservationId,
			parent,
			successfulReservations
		} = action.payload.result;

		const {
			reservation,
		} = properties;

		const reservationsServiceCategories = [];

		const isSingle = !successfulReservations;

		// single reservation
		if (isSingle) {
			const reservableId = properties.selectedReservables[0].sys_id;
			reservationsServiceCategories.push({
				reservationId,
				reservableId,
				locationServiceCategories: properties.locationsServiceCategories[reservableId]
			});

			// need to handle services that were for another location but dont apply anymore
			// no need to do this for multi as any cancelled reservations have their services cancelled,
			// and multi always cancels for non-applicable locations
			const reservationLocationId = properties.reservation && properties.reservation.location && properties.reservation.location.sys_id;
			if (reservationLocationId && reservationLocationId !== reservableId) {
				reservationsServiceCategories.push({
					reservationId,
					reservableId: reservationLocationId,
					locationServiceCategories: filterServicesToCancel(properties.locationsServiceCategories[reservationLocationId])
				});
			}
		} else {
			// multi reservation
			successfulReservations.forEach(reservation => {
				const {
					sys_id: reservationId,
					reservable: reservableId
				} = reservation;

				reservationsServiceCategories.push({
					reservationId,
					reservableId,
					locationServiceCategories: properties.locationsServiceCategories[reservableId]
				});
			});
		}

		let serviceRequestsSentCount = 0;
		if (utils.isArrayHasElement(reservationsServiceCategories)) {
			let blockerSources = {
				source: 'servicenow',
				sub_source: 'microsoft_outlook_addin',
				last_updated_source: 'external_provider',
				last_updated_sub_source: 'microsoft_outlook_addin'
			};

			// if the reservation is confirmed, last updated source is set to servicenow to trigger the sync.
			if (reservation && reservation.state.value === 'confirmed')
				blockerSources.last_updated_source = 'servicenow';

			reservationsServiceCategories.forEach(({reservationId, reservableId, locationServiceCategories}) => {
				const serviceCategories = prepareServiceCategoriesForSubmission(locationServiceCategories, properties.appointment.start);
				if (serviceCategories) {
					serviceRequestsSentCount++;
					dispatch(ACTIONS.SUBMIT_EXTRA_SERVICES_REQUEST, {
						data: {
							...blockerSources,
							reservableId,
							reservationId,
							serviceCategories
						}
					});
				}
			});
		}

		const newState = {
			...state,
			serviceRequestsSentCount,
		};

		if (serviceRequestsSentCount === 0) {
			newState.activeView = RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_SUCCESSFUL;
		}

		updateState(newState);
	},

	[ACTIONS.SAVE_RESERVATION_FAILURE]: ({ action, updateState, state, dispatch }) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;

		if (utils.isRequestUnauthorized(statusCode, statusText)) {
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});
			return;
		}

		updateState({
			...state,
			activeView: RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_FAILED
		});
	},

	[ACTIONS.SUBMIT_EXTRA_SERVICES_REQUEST_SUCCESS]: ({ action, updateState, dispatch }) => {
		updateState(({ state }) => {
			return multiEffectStateWrapper.handleRequestComplete({ ...state }, dispatch, action.payload.result.success);
		});
	},

	[ACTIONS.SUBMIT_EXTRA_SERVICES_REQUEST_FAILURE]: ({ action, updateState, dispatch }) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;

		if (utils.isRequestUnauthorized(statusCode, statusText)) {
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});
			return;
		}
			
		updateState(({ state }) => {
			return multiEffectStateWrapper.handleRequestComplete({ ...state }, dispatch, false);
		})
	},

	[ACTIONS.ALL_SUBMIT_SERVICE_REQUEST_SUCCESS]: ({ state, updateState }) => {
		updateState({ ...state, activeView: RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_SUCCESSFUL });
	},

	[ACTIONS.ALL_SUBMIT_SERVICE_REQUEST_FAILURE]: ({ state, updateState }) => {
		updateState({ ...state, activeView: RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_SUCCESSFUL });
	}
}

/**
 * Creates the reservation object to be consumed by our endpoints based on the current state and properties.
 * @param {*} viewInput
 * @returns {ReservationInput}
 */
async function createReservationObject({ action, state, properties }) {
	const officeHelper = getOfficeHelper();
	const subject = await officeHelper.getSubject();

	const {
		selectedReservables,
		appointment: {
			start,
			end,
			iCalUId,
			sensitivity
		},
		reservableModule,
		userConfig: {
			time_simple_format: timeFormat,
			timezone
		}
	} = properties;

	const reservation = properties.reservation || {};

	const {
		sys_id: reservationId,
		subject: reservationSubject,
		sensitivity: reservationSensitivity,
		locations: reservationLocations
	} = reservation;

	const validSelectedReservables = selectedReservables.filter(reservable => !!reservable.sys_id && reservable.existsInWsd);
	
	const isMulti = validSelectedReservables.length > 1;
	const isUpdate = !!reservationId;
	const reservationIsMulti = !!reservationLocations;

	const reservationObj = {
		external_ical: iCalUId,
		start,
		end,
		reservation_type: Constants.RESERVATION_TYPE_SPACE,
		reservation_purpose: Constants.RESERVATION_PURPOSE_MEETING,
		source: Constants.RESERVATION_SOURCE_EXCHANGE,
		sub_source: Constants.RESERVATION_SUB_SOURCE_OUTLOOK_ADDIN,
		last_updated_source: Constants.RESERVATION_LAST_UPDATED_SOURCE_EXTERNAL_PROVIDER,
		last_updated_sub_source: Constants.RESERVATION_SUB_SOURCE_OUTLOOK_ADDIN
	};

	if (isMulti || reservationIsMulti) {
		reservationObj.reservables = validSelectedReservables.map(selectedReservable => ({
			sys_id: selectedReservable.sys_id,
			number_of_attendees: !isUpdate ? '1' : null
		}));
	} else {
		reservationObj.location = validSelectedReservables[0].sys_id;
		if (!isUpdate)
			reservationObj.number_of_attendees = '1';
	}

	if (!isUpdate)
		reservationObj.reservable_module = reservableModule;


	// add subject and sensitivity values
	if (!isUpdate || (reservationSubject !== subject || reservationSensitivity.value !== sensitivity)) {
		reservationObj.subject = subject || t('Draft {0}', formatUTCDateTimeForUser(start, timezone, timeFormat));
		reservationObj.sensitivity = sensitivity;
	}

	return reservationObj;
}

/**
 * prepare categories service requests object (to the format expected by reservation endpoint)
 * @param {*} addedServiceCategories - added service requests grouped by categories
 * @returns {LocationServiceRequests}
 */
function prepareServiceCategoriesForSubmission(addedServiceCategories, deliveryTime) {
	if (!addedServiceCategories)
		return null;

	const addedServiceCategoriesEntries = Object.entries(addedServiceCategories);
	const serviceCategories = addedServiceCategoriesEntries.map(([categoryId, serviceCategory]) => {
		return {
			category: categoryId,
			comment: serviceCategory.comment,
			flexibleServices: serviceCategory.addedServices.map(
				({
					comment,
					flexibleServiceId,
					quantity,
					cancelFlag,
					requestId: extraServiceRequestId
				}) => ({
					comment,
					deliveryTime,
					extraServiceRequestId,
					flexibleServiceId,
					quantity,
					cancelFlag
				})
			)
		}
	});

	// if there are no categories that have atleast one service, then return null
	if (!serviceCategories.some(category => category.flexibleServices.length !== 0))
		return null;

	return serviceCategories;
}

/**
 * Returns a new serviceCategories which only contain the services which are on the server, and sets them to be cancelled.
 * @param {LocationServiceRequests} serviceCategories 
 * @returns {LocationServiceRequests}
 */
function filterServicesToCancel(serviceCategories) {
	if (!serviceCategories)
		return null;

	return utils.mapObject(serviceCategories, (categoryId, serviceCategory) => {
		let addedServices = serviceCategory.addedServices.reduce((newAddedServices, serviceRequest) => {
			if (!serviceRequest.requestId)
				return newAddedServices;

			newAddedServices.push({
				...serviceRequest,
				cancelFlag: true
			});
			return newAddedServices;
		}, []);

		return {
			...serviceCategory,
			addedServices
		};
	});
}

/**
 * check if there is any selected reservables (it could be empty if user remove it from the add-in or via outlook), move to search result view if there is none
 * @param {Reservable[]} selectedReservables 
 * @param {string} view
 * @param {*} dispatch 
 * @returns {boolean} true: if move to Result View is required
 */
function _checkAndMoveToSearchResultView(selectedReservables, view, dispatch) {
	if (view !== RESERVATION_SUMMARY_CONSTANTS.VIEWS.RESERVATION_SUMMARY || utils.isArrayHasElement(selectedReservables))
		return false;

	dispatch(ACTIONS.TRIGGER_NAVIGATION, { view: RESERVATION_SUMMARY_CONSTANTS.VIEWS.SEARCH_RESULTS });
	return true;
}

/**
 * check if the selected reservables are all valid to send to WSD
 * @param {Reservable[]} selectedReservables 
 * @param {string} type Type of the current reservation
 * @returns {boolean} - true if: has at least 1 available reservable, and no unavailable reservable. An unknown and a known avaialble one is a valid submission
 */
function _checkSubmissionEligibility(selectedReservables, type) {
	return type === APP_CONSTANTS.RECURRENCE_TYPES.SINGLE
		&& utils.isArrayHasElement(selectedReservables)
		&& selectedReservables.some(r => r.is_available && r.existsInWsd)
		&& !selectedReservables.some(r => !r.is_available);
}

/**
 * @typedef ReservationInput
 * @property {string} external_ical
 * @property {string} start
 * @property {string} end
 * @property {string} [location]
 * @property {string} [number_of_attendees]
 * @property {ReservableInput} [reservables]
 * @property {string} [subject]
 * @property {string} [sensitivity]
 * @property {string} [reservation_type]
 * @property {string} [reservation_sub_type]
 * @property {string} [reservation_purpose]
 * @property {string} [source]
 * @property {string} [sub_source]
 * @property {string} [last_updated_source]
 * @property {string} [last_updated_sub_source]
 */

/**
 * @typedef ReservableInput
 * @property {string} sys_id
 * @property {string|number} number_of_attendees
 */

/**
 * @typedef LocationServiceRequests
 * @property {string} category
 * @property {string} comment
 * @property {ServiceRequest[]} addedServices
 */

/**
* @typedef ServiceRequest
* @property {string} flexibleServiceId
* @property {string} deliveryTime
* @property {string} quantity
* @property {string} deliveryTime
*/