import { ACTIONS } from './actions';
import { t } from 'sn-translate';
import { searchReservables } from './effects/searchEffect';
import * as utils from '../utils/generalUtils';
import _isEmpty from 'lodash/isEmpty';

export const actionHandlers = {
	
	[ACTIONS.TRIGGER_SEARCH]: async ({dispatch, properties, action, state, updateState}) => {
		const {searchRequest, reservation, reservables, appointment: { start, end }} = properties;
		const resetSearchPagination = action.payload && action.payload.reset;

		if (resetSearchPagination)
			dispatch(ACTIONS.RESET_LOADED_RESERVABLES);

		const reservable_module = searchRequest.defaultReservableModule.value;
		const q = _constructEncodedQuery(properties);
		const searchRequestParam = {
			reservable_module,
			q,
			start,
			end,
			include_standard_services: false,
			include_reservable_purposes: false
		};

		if (searchRequest.pageSize)
			searchRequestParam.page_size = searchRequest.pageSize;


		if (!_isEmpty(reservation)) {
			// resolving reservation sysIds
			const reservationSysIds = [reservation.sys_id];
			const reservedReservableSysIds = [];

			if(reservation.locations) {
				// multi-reservation
				reservation.locations.map(loc => {
					reservedReservableSysIds.push(loc.sys_id);
					reservationSysIds.push(loc.reservation.sys_id);
				});
			} else {
				// single-reservation
				reservedReservableSysIds.push(reservation.location.sys_id);
			}	

			const selectedReservedReservables = filterSelectedReservedReservables(reservables, reservedReservableSysIds);

			searchRequestParam.reservation_ids = reservationSysIds.join(',');
			const selectedReservedReservablesStr = selectedReservedReservables.join(',')
			if (selectedReservedReservablesStr)
				searchRequestParam.reserved_reservables = selectedReservedReservablesStr;
		}

		if (!resetSearchPagination) {
			searchRequestParam.next_item_index = state.nextItemIndex;
		}

		dispatch(ACTIONS.REQUEST_SPACES, searchRequestParam);

		if (resetSearchPagination) {
			updateState({
				...state,
				nextItemIndex: null,
				searchHasMore: false
			});
		}
	},

	[ACTIONS.REQUEST_SPACES]: searchReservables,

	[ACTIONS.ON_SEARCH_SUCCESS]: ({action, dispatch, updateState, state}) => {
		const {payload: {result}} = action;
		const reservables = result && utils.isArrayHasElement(result.reservableUnits) ? result.reservableUnits : [];
		// make sure the responded reservable doesn't override is_selected properties
		reservables.forEach(r => {
			r.is_selected = false; 
			r.existsInWsd = true;
		});

		dispatch(ACTIONS.RESERVABLES_PAGE_LOADED, { 
			reservables
		});

		updateState({
			...state,
			hasInitSearched: true,
			nextItemIndex: result.nextItemIndex,
			searchHasMore: result.hasMore
		})
	},

	[ACTIONS.ON_SEARCH_FAILED]: ({action, dispatch}) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;

		if (utils.isRequestUnauthorized(statusCode, statusText)) {
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});
			return;
		}

		dispatch(ACTIONS.OPEN_NOW_MODAL, {
			headerLabel: t('Unable to find locations'),
			content: error.message
		});
	},

	[ACTIONS.ON_SEARCH_BUTTON_CLICKED]: ({updateState, state}) => {
		updateState({
			...state,
			hasInitSearched: false
		});
	}
};

/**
 * Helper method to construct the encoded query for the Search API
 * @param {*}
 * @return {string}
 */
 function _constructEncodedQuery(properties) {
	const {building, floors, capacity} = properties.searchRequest;
	let q = `building=${building.value}`;

	if (floors.value)
		q += `^floorIN${floors.value}`;

	if (capacity)
		q += `^capacity>=${capacity}`;

	q +=  '^emailISNOTEMPTY'
	return q;
}

/**
 * Filters out only the selected reserved reservables.
 * @param {*} reservables 
 * @param {string[]} reservedReservableSysIds 
 * @returns {string[]}
 */
function filterSelectedReservedReservables(reservables, reservedReservableSysIds) {
	return reservedReservableSysIds.filter(reservedReservableSysId => {
		const reservable = reservables.find(r => r.sys_id === reservedReservableSysId);
		return reservable && reservable.is_selected;
	})
}