import { t } from 'sn-translate';

import { isEqual as _isEqual, isEmpty as _isEmpty } from 'lodash';

/**
* Filter on reservables that are selected
* @param {*}
* @return {boolean}
*/
export function filterSelectedReservables(item) {
	return safeBool(item.is_selected);
}

/**
 * Resolve a list into a string
 * @param {Array} arr
 * @param {string} key
 * @param {string} delimiter
 * @return {string}
 */
export function resolveListIntoStr(arr, key, delimiter) {
	if (!isArrayHasElement(arr) || !key)
		return '';

	delimiter = delimiter || ',';

	return arr.reduce(function (accumulator, element, index) {
		if (_isEmpty(element[key]))
			return accumulator;

		return index === 0 ? element[key] : accumulator + delimiter + element[key];
	}, '');
}

/**
* check if a value is a true or false
* @param {*} value 
* @return {boolean}
*/
export function safeBool(value) {
	if (!value || typeof value === 'undefined')
		return false;

	return ['true', '1'].indexOf(String(value).toLowerCase().trim()) >= 0;
}

/**
* Validate that param is an array with at least one element
* @param {Array}
* @return {boolean}
*/
export function isArrayHasElement(arr) {
	return arr && Array.isArray(arr) && arr.length > 0;
}

/**
 * Applies the callback to each of the values in the object.
 * @param {object} obj 
 * @param {MapObjectCallback}  
 * @returns {object}
 */
export function mapObject(obj, callback) {
	if (!obj || !callback)
		return obj;

	let objEntries = Object.entries(obj);
	objEntries = objEntries.map(([objKey, objValue]) => [objKey, callback(objKey, objValue)]);
	return Object.fromEntries(objEntries);
}

/**
 * Generates a GUID
 * @returns {string}
 */
export function generateGUID() {
	var dt = new Date().getTime();
	var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
		var r = (dt + Math.random()*16)%16 | 0;
		dt = Math.floor(dt/16);
		return (c=='x' ? r :(r&0x3|0x8)).toString(16);
	});

	return uuid;
}

/**
 * Calculates the totalQuantity for a serviceCategory
 * @param {*} addedServices 
 * @returns {number}
 */
export function calculateServiceCategoryQuantity(addedServices) {
	return calculateTotalNumberInServiceCategory(addedServices, 'quantity');
}

/**
 * Calculates the totalPrice for a serviceCategory
 * @param {*} addedServices 
 * @returns {number}
 */
export function calculateServiceCategoryPrice(addedServices) {
	return calculateTotalNumberInServiceCategory(addedServices, 'totalPrice');
}

export function calculateTotalNumberInServiceCategory(addedServices, field) {
	return addedServices.reduce((totalVal, addedService) => {
		const val = parseFloat(addedService[field]);
		if (addedService.cancelFlag || isNaN(val) || !addedService.flexibleServiceId)
			return  totalVal;

		return val + totalVal;
	}, 0);
}

/**
 * If values are string, and user allows caseInsensitive, then checks if the strings match without case, else uses lodash isEqual
 * @param {*} val1
 * @param {*} val2
 * @param {boolean} [caseInsensitive]
 * @returns {boolean}
 */
export function isEqual(val1, val2, caseInsensitive) {
	if (caseInsensitive && typeof val1 === 'string' && typeof val2 === 'string')
		return val1.toLowerCase() === val2.toLowerCase();

	return _isEqual(val1, val2);
}

/**
 * Check if input is a valid number
 * @param {*} input 
 * @return {boolean}
 */
export function isValidNumber(input) {
	const number = Number(input);
	return !isNaN(number);
}

/**
 * @callback MapObjectCallback
 * @param {string} id
 * @param {*} value
 */

/**
 * Construct a full location string - contains campus, building and floor
 * @param {*} {locationDetails}
 * @return {string}
 */
 export function constructLocationDescription(locationDetails) {
	if (!locationDetails || !locationDetails.existsInWsd)
		return t('Item can not be recognized in WSD');

	const fields = ['campus', 'building', 'floor'];
	const result = [];

	fields.forEach(field => {
		if (locationDetails[field] && locationDetails[field].display_value)
			result.push(locationDetails[field].display_value);
	});

	return result.join(', ');
}

/**
 * Check whether the request was unauthorized (based on status code/text)
 * @param {string} code
 * @param {string} text
 * @return {boolean}
 */
export function isRequestUnauthorized(code, text) {
	return code === 401 || String(text.toLowerCase()) === 'unauthorized';
}