import { t } from 'sn-translate';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import moment from 'moment';
import '@servicenow/now-text-link';
import '@servicenow/now-icon';
import '@servicenow/now-heading';

import '../sn-wsd-moa-input-wrapper';
import { ACTIONS } from './actions';
import { formatUTCDateTimeForUser } from '../utils/moaDateTimeUtils';
import * as utils from '../utils/generalUtils';

export const view = (state, { dispatch, updateState }) => {
	const {
		serviceRequest,
		subCategory,
		deliveryTime,
		userConfig
	} = state.properties;

	if (!serviceRequest || !subCategory)
		return null;

	const {
		flexibleServiceId,

		quantity,
		comment,
		currencyCode,
		totalPrice,
		subCategory: {
			display_value: subCategoryLabel
		}
	} = serviceRequest;

	const deliveryTimeDisplay = formatUTCDateTimeForUser(deliveryTime, userConfig.timezone, userConfig.time_simple_format);

	return (
		<div className='service-request-card card'>
			<div className='card__header'>
				<h3 className='card__header-left'>
					<now-heading
						label={subCategoryLabel}
						variant="title-primary"
					/>
				</h3>
				<now-text-link
					label={ t('Remove') }
					href="javascript:void(0)"
					on-click={() => dispatch(ACTIONS.REMOVE_EXTRA_SERVICE_REQUEST, {serviceRequest})}
					className='card__header-right'
				/>
			</div>
			
			<div className='service-request-card__section'>
				<sn-wsd-moa-input-wrapper label={subCategoryLabel} mandatory={true} empty={!flexibleServiceId}>
					<select on-change={ (event, element) => onSelectedServiceChanged(dispatch, state, subCategory, element.elm.value) } className='input-default'>
						{constructFlexibleServiceOptionsView(dispatch, state, subCategory, flexibleServiceId)}
					</select>
				</sn-wsd-moa-input-wrapper>
			</div>

			<div className='service-request-card__section'>
				<sn-wsd-moa-input-wrapper className='service-request-card__delivery-time' label={ t('Delivery time') } disabled={true}>
					<input type='text' value={deliveryTimeDisplay} className='input-default' disabled />
				</sn-wsd-moa-input-wrapper>

				<sn-wsd-moa-input-wrapper className='service-request-card__quantity' label={ t('Quantity') } mandatory={true}>
					<input type='number' min='1' value={quantity} className='input-default' on-change={(event, field) => onQuantityChanged(dispatch, state, field)}/>
				</sn-wsd-moa-input-wrapper>

				<sn-wsd-moa-input-wrapper className='service-request-card__cost' label={ t('Cost') } disabled={true}>
					<div className="ellipsis">{ !flexibleServiceId ? '' : `${currencyCode} ${totalPrice.toFixed(2)}` }</div>
				</sn-wsd-moa-input-wrapper>
			</div>
			<div className='service-request-card__section'>
				{
					!comment && !state.commentOpen ?
						(
							<now-text-link
								label={(
									<Fragment>
										<now-icon icon='pencil-outline' size='sm' />
										{ ' ' + t('Add a comment')}
									</Fragment>
								)}
								href="javascript:void(0)"
								on-click={() => updateState({commentOpen: true})}
								className='service-request-card__add-comments-link'
							/>
						) :
						(
							<sn-wsd-moa-input-wrapper label={ t('Comments') }>
								<textarea className='input-default' autofocus value={comment} on-change={ (event, element) => updateServiceRequest(dispatch, state, { comment: element.elm.value }) }/>
							</sn-wsd-moa-input-wrapper>
						)
				}
			</div>
		</div>
	);
};

/**
 * Sends an updated state of the service request
 * @param {*} newState 
 */
function updateServiceRequest(dispatch, state, newState) {
	dispatch(ACTIONS.UPDATE_EXTRA_SERVICE_REQUEST, {
		serviceRequest: {
			...state.properties.serviceRequest,
			...newState
		}
	});
};

/**
 * Handles quantity change, and handles updating quantity on the service request state
 * @param {*} event 
 * @param {*} field 
 */
function onQuantityChanged(dispatch, state, field) {
	const quantityField = field.elm;
	let newQuantity = parseFloat(quantityField.value);

	if (isNaN(newQuantity) || newQuantity < 1) {
		newQuantity = 1;
		quantityField.value = 1; // need to update the element directly as state doesnt always actually change
	}

	updateServiceRequest(dispatch, state, {
		quantity: newQuantity
	});
}

function onSelectedServiceChanged(dispatch, state, subCategory, selectedService) {
	const flexibleService = subCategory.flexible_service.find(flexibleService => flexibleService.sys_id === selectedService);

	updateServiceRequest(dispatch, state, {
		flexibleServiceId: selectedService,
		flexibleServiceName: flexibleService.name,
		servicePrice: flexibleService.price_per_unit.value,
		currencyCode: flexibleService.price_per_unit.code,
	});
}

/**
 * Construct the view for the flexible service options
 * @param {*} dispatch 
 * @param {*} state 
 * @param {Object} subCategory
 * @param {string} flexibleServiceId
 * @returns {*}
 */
function constructFlexibleServiceOptionsView(dispatch, state, subCategory, flexibleServiceId) {
	const {flexible_service: services} = subCategory;

	if (!utils.isArrayHasElement(services))
		return;

	if (!flexibleServiceId) {
		if (services.length === 1) {
			const service = services[0];
			onSelectedServiceChanged(dispatch, state, subCategory, service.sys_id);

			return (
				<option
					value={service.sys_id}
					selected={true}>
					{service.name}
				</option>
			);
		} else {
			return (
				<Fragment>
					<option value="">{t('Select')}</option>
					{services.map(service => (
						<option value={service.sys_id}>
							{service.name}
						</option>
					))}
				</Fragment>
			);
		}
	}

	return (
		services.map(service => (
			<option
				value={service.sys_id}
				selected={flexibleServiceId === service.sys_id}>
				{service.name}
			</option>
		))
	);
}