import {createHttpEffect} from '@servicenow/ui-effect-http';

import { ACTIONS } from '../actions';

const RESERVATION_BASE_URL = '/api/sn_wsd_rsv/reservation';
const MULTI_RESERVATION_BASE_URL = '/api/sn_wsd_rsv/multi_reservation';

export const insertReservation = createHttpEffect(`${RESERVATION_BASE_URL}/add`, {
	method: 'POST',
	dataParam: 'data',
	successActionType: ACTIONS.SAVE_RESERVATION_SUCCESS,
	errorActionType: ACTIONS.SAVE_RESERVATION_FAILURE
});

export const updateReservation = createHttpEffect(`${RESERVATION_BASE_URL}/update/:id`, {
	method: 'PATCH',
	pathParams: ['id'],
	dataParam: 'data',
	successActionType: ACTIONS.SAVE_RESERVATION_SUCCESS,
	errorActionType: ACTIONS.SAVE_RESERVATION_FAILURE
});

export const insertMultiReservation = createHttpEffect(`${MULTI_RESERVATION_BASE_URL}/add`, {
	method: 'POST',
	dataParam: 'data',
	successActionType: ACTIONS.SAVE_RESERVATION_SUCCESS,
	errorActionType: ACTIONS.SAVE_RESERVATION_FAILURE
});

export const updateMultiReservation = createHttpEffect(`${MULTI_RESERVATION_BASE_URL}/update/:id`, {
	method: 'PATCH',
	pathParams: ['id'],
	dataParam: 'data',
	successActionType: ACTIONS.SAVE_RESERVATION_SUCCESS,
	errorActionType: ACTIONS.SAVE_RESERVATION_FAILURE
});

export default {
	insertReservation,
	updateReservation,
	insertMultiReservation,
	updateMultiReservation
};