import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import { view } from './view';
import styles from './styles.scss';
import { ACTIONS } from './actions';
import { actionHandlers } from './actionHandlers';

createCustomElement('sn-wsd-moa-services-manager', {
	renderer: {type: snabbdom},
	view,
	properties: {
		location: {
			default: null
		},
		selectedServices: {
			default: []
		},
		appointment: {
			default: null
		},
		userConfig: {
			default: null
		}
	},
	initialState: {
		selectedCategory: null,
		subTotalPrice: {value: 0, currencyCode: 'EUR'},
		selectedServices: [],
		hasErrors: false
	},
	styles,
	actions: ACTIONS,
	actionHandlers
});
