import { t } from 'sn-translate';
import '@servicenow/now-record-checkbox';
import '@servicenow/now-icon';
import '@servicenow/now-highlighted-value';

import { ACTIONS } from './actions';

export const view = (state, {dispatch}) => {
	const {properties: {reservable}} = state;
	const isEligibleForSubmit = reservable.is_available && reservable.existsInWsd;
	const reservableCssClass = isEligibleForSubmit ? '' :'disable-card';

	return (
		<div className={`space-card ${reservableCssClass}`}>
			<div className="space-card-actions">
				<now-record-checkbox
					config-aria={{'aria-label': t(`Select the space ${reservable.name}`)}}
					name={`space-card__checkbox-${reservable.sys_id}`}
					value={reservable.is_selected}
					onValueChange={(event) => {
						dispatch(ACTIONS.RESERVABLE_SELECTED, {
							...reservable,
							is_selected: String(event.value)
						});
					}}
					readonly={!isEligibleForSubmit}
				/>
			</div>
			<div className="space-card-main">
				<div className="space-card-main__title">
					{reservable.name}
				</div>
				{
					isEligibleForSubmit ? '' : 
					<div className="space-card-main__availability">
						<now-highlighted-value 
							label={reservable.existsInWsd? t('Unavailable') : t('Unknown')} 
							color="critical" 
							show-icon>
						</now-highlighted-value>
					</div>
				}
				<div className="space-card-main__subtitle">
					<now-icon icon="user-group-outline" size="md"/> {reservable.capacity}
				</div>
			</div>
		</div>
	)
};