/**
 * Helper class to handle updating the state based on completed http effects
 */
export default class MultiEffectStateWrapper {

	_completedCountKey = '';
	_totalCountKey = '';
	_isFailedKey = '';

	_successAction = '';
	_failureAction = '';

	/**
	 * Initializes the required fields for the class to work
	 * @param {MultiEffectStateWrapperConstructor} multiEffectStateWrapperInput
	 */
	constructor({ completedCountKey, totalCountKey, isFailedKey, successAction, failureAction }) {
		this._completedCountKey = completedCountKey;
		this._totalCountKey = totalCountKey;
		this._isFailedKey = isFailedKey;
		this._successAction = successAction;
		this._failureAction = failureAction;
	}

	/**
	 * Resets the required fields to begin another multi request
	 * @param {*} state 
	 * @returns {*} new state
	 */
	reset(state) {
		state[this._completedCountKey] = 0;
		state[this._totalCountKey] = 0;
		state[this._isFailedKey] = false;
		return state;
	}

	/**
	 * Handles updating the state upon a completion of a request
	 * @param {*} state 
	 * @param {callback} dispatch 
	 * @param {boolean} success - whether or not the request was successful 
	 * @returns {*} new state
	 */
	handleRequestComplete(state, dispatch, success) {
		const newCompletedCount = state[this._completedCountKey] + 1;
		const failed = state[this._isFailedKey] || !success;
		
		if (newCompletedCount === state[this._totalCountKey])
			this.dispatchOnComplete(state, dispatch, !failed);

		state[this._completedCountKey] = newCompletedCount;
		state[this._isFailedKey] = failed;
		return state;
	}

	/**
	 * Dispatches success or failure based on whether the multi request was successful
	 * @param {*} state 
	 * @param {callback} dispatch 
	 * @param {boolean} success - whether or not the multi request was successful
	 */
	dispatchOnComplete(state, dispatch, success) {
		// set timeout so that the dispatch only occurs after the state is returns and updated.
		setTimeout(() => {
			dispatch(success ? this._successAction : this._failureAction);
		}, 0);
	}
}

/**
 * @typedef MultiEffectStateWrapperConstructor
 * @property {string} completedCountKey
 * @property {string} totalCountKey
 * @property {string} isFailedKey
 * @property {string} successAction
 * @property {string} failureAction
 */