import '@servicenow/now-icon';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

export const view = (state) => {
	const {
		label,
		mandatory,
		disabled,
		empty,
		showError
	} = state.properties;

	const containerClassNames = ['moa-input'];
	if (!disabled)
		containerClassNames.push('moa-input--enabled');

	if (showError && empty)
		containerClassNames.push('moa-input--error');

	const mandatoryClassNames = ['moa-input__mandatory-icon'];
	if (empty)
		mandatoryClassNames.push('moa-input__mandatory-icon--empty');

	return (
		<div className={containerClassNames.join(' ')}>
			<div className='moa-input__header'>
				{label}
				{
					!mandatory ? '' :
						<now-icon size='sm' icon='asterisk-fill' className={mandatoryClassNames.join(' ')} />
				}
			</div>
			<slot />
		</div>
	)
};