import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import { ACTIONS } from './actions';
import { view } from './view';
import { actionHandlers } from './actionHandlers';

createCustomElement('sn-wsd-moa-search', {
	renderer: {type: snabbdom},
	properties: {
		searchRequest: {
			default: {}
		},
		appointment: {
			default: null
		},
		reservables: {
			default: []
		},
		showFilter: {
			default: true
		},
		reservation: {
			default: null
		},
		hasInitSearched: {
			default: false
		}
	},
	initialState: {
		hasInitSearched: false,
		nextItemIndex: null,
		searchHasMore: false
	},
	view,
	actions: ACTIONS,
	actionHandlers
});
