import { t } from 'sn-translate';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-icon';

import '../sn-wsd-moa-header';
import '../sn-wsd-moa-layout';
import '../sn-wsd-moa-service-request-card';
import '../sn-wsd-moa-input-wrapper';
import '../sn-wsd-moa-price-label';
import { ACTIONS } from './actions';
import { CONSTANTS } from '../sn-wsd-moa-app/constants';

export const view = (state, triggers) => {
	const {
		subTotalPrice,
		properties: {
			location: reservable
		}
	} = state;

	if (!reservable) {
		return (
			<div className='services-manager'>
				{ generateErrorBody(state, triggers) }
			</div>
		);
	}

	return (
		<div className='services-manager unselectable' tabindex={0}>
			<sn-wsd-moa-layout>
				<div slot='layoutHeader'>
					<sn-wsd-moa-header
						title={reservable.name}
						onBack={() => triggers.dispatch(ACTIONS.TRIGGER_NAVIGATION, { view: CONSTANTS.VIEWS.SUMMARY})}
					/>
				</div>
				<div slot='layoutBody' className='services-manager__body'>
					{
						reservable.flexible_services.map(category => {
							return generateCategory(state, triggers, category);
						})
					}
				</div>
				<div slot='layoutFooter' className='services-manager__footer'>
					<div className='services-manager__subtotal-amount'>
						<sn-wsd-moa-price-label
							className="text-uppercase"
							mode='large'
							label={t('Subtotal ({0})', subTotalPrice.currencyCode)}
							value={subTotalPrice.value.toFixed(2)}
						/>
					</div>
					<div className='action-buttons'>
						<now-button
							label={ t('Cancel') }
							size='md'
							on-click={ () => triggers.dispatch(ACTIONS.TRIGGER_NAVIGATION, { view: CONSTANTS.VIEWS.SUMMARY}) } />
						<now-button
							label={ t('Save services') }
							variant='primary'
							size='md'
							disabled={state.hasErrors}
							on-click={ () => triggers.dispatch(ACTIONS.SAVE_SERVICE_REQUESTS) } />
					</div>
				</div>
			</sn-wsd-moa-layout>
		</div>
	);
}

/**
 * Generates a single categories accordion item
 * @param {*} category 
 * @param {*} state 
 */
function generateCategory(state, triggers, category) {
	const categorySelection = state.selectedServices[category.category];
	const categoryIsActive = category.category === state.selectedCategory;
	let categoryClassName = 'services-manager-category';

	if (!categoryIsActive)
		categoryClassName = `${categoryClassName} services-manager-category--closed`;

	return (
		<div className={categoryClassName}>
			<div className='services-manager-category-header'
				on-click={() => triggers.dispatch(ACTIONS.ACCORDION_CLICK, {value: !categoryIsActive, id: category.category})}>
				<span className='services-manager-category-header__title'>{category.label || t('Category name')}</span>
				{
					// generate each accordions right hand data
					categoryIsActive ? '' : (
						<Fragment>
							<span
								className='services-manager-category-header__amount'
								title={getCategoryPriceLabel(state, category.category)}>
								{getCategoryMetadata(state, triggers, category)}
							</span>
							{
								!categorySelection || !categorySelection.hasErrors ? '' : (
									<now-icon icon='circle-exclamation-fill' size='md' className='services-manager-category-header__error-icon' />
								)
							}
						</Fragment>
					)
				}
				<now-button-iconic
					bare
					icon={categoryIsActive ? 'chevron-up-fill' : 'chevron-down-fill'}
					variant="tertiary"
					on-click={() => triggers.dispatch(ACTIONS.ACCORDION_CLICK, {value: !categoryIsActive, id: category.category})}
					hidePadding={true}
					config-aria={{'aria-label': categoryIsActive ? t('Collapse {0}', category.label) : t('Expand {0}', category.label)}}
				/>
			</div>
			{ categoryIsActive ? generateCategoryContent(state, triggers, category) : '' }
		</div>
	);
}

/**
 * Generates a single categories accordion items content
 * @param {*} category
 */
function generateCategoryContent(state, triggers, category) {
	const selectedCategoryServices = state.selectedServices[category.category];
	const hasServices = selectedCategoryServices
		&& Array.isArray(selectedCategoryServices.addedServices)
		&& selectedCategoryServices.addedServices.some((service) => !service.cancelFlag);

	return (
		<div className='services-manager-category-content'>
			{
				hasServices
					? generateCategoryServiceRequests(state, triggers, category)
					: generateInitialServicesView(state, triggers, category)
			}
		</div>
	);
}

/**
 * Generates the sub-categories add link for the category
 * @param {*} state 
 * @param {*} triggers 
 * @param {*} category 
 */
function generateInitialServicesView(state, triggers, category) {
	return (
		<Fragment>
			<div className='services-manager-category-content__header'>{ t('What do you want to add to your reservation?') }</div>
			{
				category.flexible_service.map(subCategory => (
					<div className='services-manager-category-content__subcategory-link'>
						<now-button
							bare
							label={subCategory.label}
							size="sm"
							variant="secondary"
							icon="plus-fill"
							on-click={() => dispatchAddExtraServiceRequest(triggers.dispatch, category.category, subCategory.sub_category)}
						/>
					</div>
				))
			}
		</Fragment>
	)
}

/**
 * Generates the service requests for the category, and the select to add more
 * @param {*} state 
 * @param {*} triggers 
 * @param {*} category 
 */
function generateCategoryServiceRequests(state, triggers, category) {
	const selectedCategoryServices = state.selectedServices[category.category];

	return (
		<Fragment>
			{
				selectedCategoryServices.addedServices.map((selectedService, index) => {
					if (selectedService.cancelFlag)
						return '';
					
					return (
						<sn-wsd-moa-service-request-card
							serviceRequest={selectedService}
							subCategory={category.flexible_service.find(subCategory => subCategory.sub_category === selectedService.subCategory.value)}
							deliveryTime={state.properties.appointment.start}
							userConfig={state.properties.userConfig} />
					);
				})
			}
			<sn-wsd-moa-input-wrapper>
				<select className='input-default' on-change={ (event, element) => dispatchAddExtraServiceRequest(triggers.dispatch, category.category, element.elm.value) }>
					{ /* By using the length of the added services,
					* everytime the length changes, ie a new service is added
					* the selection is reset to the default */}
					<option selected={selectedCategoryServices.addedServices.length} value="">{category.additional_text}</option>
					{
						category.flexible_service.map(subCategory => (
							<option value={subCategory.sub_category}>{subCategory.label}</option>
						))
					}
				</select>
			</sn-wsd-moa-input-wrapper>
		</Fragment>
	);
}

/**
 * Default view for error body for service manager
 */
function generateErrorBody(state, {dispatch}) {
	return (
		<div class='services-manager__error'>
			<sn-wsd-moa-header
				title={"Error"}
				onBack={() => dispatch(ACTIONS.TRIGGER_NAVIGATION, { view: CONSTANTS.VIEWS.SUMMARY})}  />
			{ t('Something went wrong') }
		</div>
	);
}

/**
 * Dispatches the event to add new extra service request
 * @param {callback} dispatch
 * @param {string} category
 * @param {string} subCategory
 */
function dispatchAddExtraServiceRequest(dispatch, category, subCategory) {
	dispatch(ACTIONS.ADD_EXTRA_SERVICE_REQUEST, {category, subCategory})
}

function getCategoryMetadata(state, triggers, category) {
	const categorySelectedServices = state.selectedServices[category.category];
	if (!categorySelectedServices || categorySelectedServices.totalQuantity === 0)
		return '';

	return (
		<sn-wsd-moa-price-label
			mode='medium'
			label={t('{0} item(s):', categorySelectedServices.totalQuantity)}
			value={`${categorySelectedServices.categoryCurrencyCode} ${categorySelectedServices.totalPrice.toFixed(2)}`}
		/>
	);
}

/**
 * Get the full price label string for a single category
 * @param {*} selectedServices 
 * @param {string} categoryName 
 * @returns {string}
 */
function getCategoryPriceLabel({selectedServices}, categoryName) {
	const categorySelectedServices = selectedServices[categoryName];
	if (!categorySelectedServices || categorySelectedServices.totalQuantity === 0)
		return '';

	const {
		totalPrice,
		totalQuantity,
		categoryCurrencyCode
	} = categorySelectedServices;
		
	return t('{0} item(s): {1} {2}', [totalQuantity, categoryCurrencyCode, totalPrice.toFixed(2)]);
}