/**
 * Static utilitily functions
 */

import moment from 'moment-timezone';

/**
 * convert date representation data to UTC format string
 * @param {string|DateTime} date - either a string or a javascript Date object
 * @return {string}
 */
export function convertToUTC(date) {
	if (!date) return null;

	return moment.utc(date).format();
}

export function formatUTCDateTimeForUser(dateTime, timeZone, format) {
	if (!format && !timeZone) {
		return dateTime;
	}

	const formattedDateTime = moment.utc(dateTime);
	formattedDateTime.tz(timeZone);
	return formattedDateTime.format(format);
}