import {actionTypes} from '@servicenow/ui-core';
import cloneDeep from 'lodash/cloneDeep';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;

const SUB_CATEGORY_MODEL = {
	label: 'Sub Category Label',
	addedServices: [],
	totalPrice: 0,
	totalQuantity: 0,
	servicesLabels: [],
	servicesIds: [],
};

export const actionHandlers = {

	[COMPONENT_BOOTSTRAPPED]: ({ properties, state, updateState }) => {
		// calculate all display items, such as categories and service labels when the component is loaded as it wont change here.
		const servicesSummary = properties.serviceRequests.addedServices.reduce((serviceRequestsBySubCategories, serviceRequest) =>  {
			if (serviceRequest.cancelFlag)
				return serviceRequestsBySubCategories;

			const subCategoryId = serviceRequest.subCategory.value;
			const subCategoryServiceRequests = serviceRequestsBySubCategories[subCategoryId] || cloneDeep(SUB_CATEGORY_MODEL);

			subCategoryServiceRequests.totalPrice += serviceRequest.totalPrice;
			subCategoryServiceRequests.totalQuantity += serviceRequest.quantity;
	
			if (subCategoryServiceRequests.servicesIds.indexOf(serviceRequest.flexibleServiceId) === -1) {
				subCategoryServiceRequests.servicesIds.push(serviceRequest.flexibleServiceId);
				subCategoryServiceRequests.servicesLabels.push(serviceRequest.flexibleServiceName);
				subCategoryServiceRequests.label = serviceRequest.subCategory.display_value;
			}

			serviceRequestsBySubCategories[subCategoryId] = subCategoryServiceRequests;
			return serviceRequestsBySubCategories;
		}, {});

		updateState({
			...state,
			servicesSummary
		});
	},
}