import {actionTypes} from '@servicenow/ui-core';
const {COMPONENT_BOOTSTRAPPED, COMPONENT_DOM_READY} = actionTypes;
import cloneDeep from 'lodash/cloneDeep';

import { ACTIONS } from './actions';
import * as utils from '../utils/generalUtils';

const CATEGORY_MODEL = {
	comment: '',
	categoryFriendlyName: '',
	categoryCurrencyCode: '',
	addedServices: [],
	totalPrice: 0,
	totalQuantity: 0,
	hasErrors: false
};

const SERVICE_REQUEST_MODEL = {
	clientId: '',
	category: '',
	categoryFriendlyName: '',
	subCategory: {
		value: '',
		display_value: ''
	},
	flexibleServiceId: '',
	flexibleServiceName: '',
	servicePrice: 0,
	currencyCode: '',
	totalPrice: 0,
	quantity: 1,
	comment: '',
};

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ properties, state, updateState }) => {
		const selectedServices = utils.mapObject(properties.selectedServices, (selectServiceEntryId, selectedServiceEntryValue) => {
			return {
				...selectedServiceEntryValue,
				totalPrice: parseFloat(selectedServiceEntryValue.totalPrice),
				totalQuantity: utils.calculateServiceCategoryQuantity(selectedServiceEntryValue.addedServices),
				hasErrors: false
			}
		});

		const newState = _updateCalculatedStateProperties(state, selectedServices);
		updateState(newState);
	},

	[COMPONENT_DOM_READY]: ({action}) => {
		const {payload: {host}} = action;
		const servicesManager = host.shadowRoot.querySelector('.services-manager');
		
		if (servicesManager)
			servicesManager.focus();
	},

	[ACTIONS.ACCORDION_CLICK]: ({action, state, updateState}) => {
		const {
			id,
			value
		} = action.payload;

		const selectedCategory = value ? id : null;

		updateState({
			...state,
			selectedCategory
		});
	},

	[ACTIONS.ADD_EXTRA_SERVICE_REQUEST]: ({action, state, properties, updateState}) => {
		const {
			category: categoryId,
			subCategory: subCategoryId
		} = action.payload;

		const category = properties.location.flexible_services.find(locationCategory => locationCategory.category === categoryId);
		const subCategory = category.flexible_service.find(subCategory => subCategory.sub_category === subCategoryId);
		
		let categorySelectedServices = state.selectedServices[categoryId];
		if (!categorySelectedServices) {
			categorySelectedServices = {
				...cloneDeep(CATEGORY_MODEL),
				categoryFriendlyName: category.label,
				categoryCurrencyCode: category.currency_code
			};
		} else
			categorySelectedServices = {...categorySelectedServices};

		const newExtraServiceRequest = {
			...cloneDeep(SERVICE_REQUEST_MODEL),
			clientId: utils.generateGUID(),
			category: category.category,
			categoryFriendlyName: category.label,
			subCategory: {
				value: subCategory.sub_category,
				display_value: subCategory.label
			},
		};

		categorySelectedServices.addedServices = [...categorySelectedServices.addedServices, newExtraServiceRequest];
		categorySelectedServices.hasErrors = true;

		updateState({
			...state,
			selectedServices: {
				...state.selectedServices,
				[categoryId]: categorySelectedServices
			},
			hasErrors: true
		})
	},

	[ACTIONS.UPDATE_EXTRA_SERVICE_REQUEST]: ({ action, state, updateState }) => {
		let newServiceRequestState = {...action.payload.serviceRequest};

		const {
			clientId,
			category
		} = newServiceRequestState;

		const selectedServiceCategory = state.selectedServices[category];
		const oldAddedServices = selectedServiceCategory.addedServices;
		const addedServices = oldAddedServices.concat();
		const addedServicesIndex = oldAddedServices.findIndex(serviceRequest => serviceRequest.clientId === clientId);

		if (!newServiceRequestState.cancelFlag)
			newServiceRequestState = _updateServiceRequestCalculatedProperties(newServiceRequestState);

		addedServices[addedServicesIndex] = newServiceRequestState;

		_updateAddedServicesState(updateState, state, category, addedServices);
	},

	[ACTIONS.REMOVE_EXTRA_SERVICE_REQUEST]: ({ action, state, updateState, dispatch }) => {
		const serviceRequest = action.payload.serviceRequest;
		const {
			requestId,
			clientId,
			category
		} = serviceRequest;

		if (requestId) {
			dispatch(ACTIONS.UPDATE_EXTRA_SERVICE_REQUEST, {
				serviceRequest: {
					...serviceRequest,
					cancelFlag: true	
				},
			});
			return;
		}

		const oldAddedServices = state.selectedServices[category].addedServices;
		const addedServices = oldAddedServices.filter(serviceRequest => serviceRequest.clientId !== clientId);

		_updateAddedServicesState(updateState, state, category, addedServices);
	},

	[ACTIONS.SAVE_SERVICE_REQUESTS]: ({ state, dispatch, properties }) => {
		if (state.hasErrors) {
			console.error('Trying to save services when there are errors present.');
			return;
		}

		dispatch(ACTIONS.UPDATE_SERVICE_REQUESTS, { locationId: properties.location.sys_id, locationExtraServiceRequests: state.selectedServices });
	}
}

/**
 * Updates the service requests calculated properties
 * @param {*} serviceRequest
 * @returns {*} returns new service request state
 */
function _updateServiceRequestCalculatedProperties(serviceRequest) {
	return {
		...serviceRequest,
		totalPrice: serviceRequest.servicePrice * serviceRequest.quantity
	};
}

/**
 * Updates the addedServices, and updates the calculated properties based on the addedServices
 * @param {*} updateState
 * @param {*} state
 * @param {*} category
 * @param {*} addedServices
 */
function _updateAddedServicesState(updateState, state, category, addedServices) {
	const newSelectedServices ={
		...state.selectedServices,
		[category]: {
			...state.selectedServices[category],
			addedServices,
			totalPrice: utils.calculateServiceCategoryPrice(addedServices),
			totalQuantity: utils.calculateServiceCategoryQuantity(addedServices),
			hasErrors: !_areServiceRequestsValid(addedServices)
		}
	};

	const calculatedNewState = _updateCalculatedStateProperties(state, newSelectedServices);
	updateState(calculatedNewState);
}

/**
 * Updates the calculated values of the state. such as total price of each category
 * @param {*} state
 * @returns {*} - returns the new state
 */
function _updateCalculatedStateProperties(state, selectedServices) {
	const {subTotalPrice} = state;
	const categoryEntries = Object.entries(selectedServices);

	const newSubTotalPrice = categoryEntries.reduce((accumulator, categoryEntry) => {
		const category = categoryEntry[1];
		return {
			value: accumulator.value + category.totalPrice,
			currencyCode: category.categoryCurrencyCode !== accumulator.currencyCode ? category.categoryCurrencyCode : accumulator.currencyCode
		};
	}, { value: 0, currencyCode: subTotalPrice.currencyCode });

	return {
		...state,
		selectedServices,
		subTotalPrice: newSubTotalPrice,
		hasErrors: Object.entries(selectedServices).some(([categoryId, servicesCategory]) => servicesCategory.hasErrors)
	};
}

/**
 * Validates that all services of the provided category are valid
 * @param {*} addedServices 
 * @returns {boolean}
 */
function _areServiceRequestsValid(addedServices) {
	return !addedServices.some(addedService =>
		!addedService.cancelFlag && (
			!addedService.flexibleServiceId
			|| typeof addedService.flexibleServiceId !== 'string'
			|| addedService.quantity < 1
			|| typeof addedService.quantity !== 'number'	
		)
	);
}