import {createHttpEffect} from '@servicenow/ui-effect-http';

import { ACTIONS } from '../actions';

const EXTRA_SERVICE_REQUEST_BASE_URL = '/api/sn_wsd_rsv/wsd_extra_service_request';

export const submitExtraServicesRequest = createHttpEffect(`${EXTRA_SERVICE_REQUEST_BASE_URL}/submit`, {
	method: 'POST',
	dataParam: 'data',
	successActionType: ACTIONS.SUBMIT_EXTRA_SERVICES_REQUEST_SUCCESS,
	errorActionType: ACTIONS.SUBMIT_EXTRA_SERVICES_REQUEST_FAILURE
})

export default {
	submitExtraServicesRequest
};