
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-alert';
import '@servicenow/now-modal';
import '@servicenow/now-loader';

import * as utils from '../utils/generalUtils';
import { CONSTANTS } from './constants';
import '../sn-wsd-moa-search';
import '../sn-wsd-moa-reservation-summary';
import '../sn-wsd-moa-services-manager';
import '../sn-wsd-moa-diagnostic';

export const view = (state) => {
	const {appInitCompleted, enableDiagnostic, userConfig, notificationConfig: {opened, headerLabel, content, footerActions}} = state;

	return (
		<div className="sn-wsd-moa-app">
			<now-modal
				size="sm"
				opened={opened}
				headerLabel={headerLabel}
				content={content}
				footerActions={footerActions}
			/>

			{!appInitCompleted 
				? ( getLoadingIndicator(t('Preparing the add-in...')) )
				: ( 				
					<Fragment>
						<div className='sn-wsd-moa-app__container display-flex-column'>
							<div className='flex-1'>
								{ renderActiveView(state) }
							</div>
							{!enableDiagnostic ? '' : (
								<div className='flex-0'>
									<sn-wsd-moa-diagnostic userConfig={userConfig}/>
								</div>
							)}
						</div>
						
					</Fragment>
				)
			}
		</div>
	);
};

/**
* get active view (whether it's a search screen or summary etc...)
* @param {*} state
*/
const getActiveView = (state) => {
	const { activeView } = state;

	switch (activeView) {
		case CONSTANTS.VIEWS.SEARCH_RESULTS:
		case CONSTANTS.VIEWS.SEARCH_FILTER: {
			const { searchRequest, appointment, reservation, reservables } = state;
			return (
				<sn-wsd-moa-search
					searchRequest={searchRequest}
					appointment={appointment}
					reservables={reservables}
					reservation={reservation}
					showFilter={activeView === CONSTANTS.VIEWS.SEARCH_FILTER}
				/>
			);
		}
		case CONSTANTS.VIEWS.SUMMARY: {
			const { searchRequest, appointment, reservables, locationsServiceCategories, locationsDetails, reservation, userConfig } = state;
			return (
				<sn-wsd-moa-reservation-summary
					appointment={appointment}
					selectedReservables={reservables.filter(utils.filterSelectedReservables)}
					reservableModule={searchRequest.defaultReservableModule.value}
					locationsServiceCategories={locationsServiceCategories}
					locationsDetails={locationsDetails}
					reservation={reservation}
					userConfig={userConfig}
				/>
			);
		}
		case CONSTANTS.VIEWS.SERVICES_MANAGER: {
			const {appointment, locationsServiceCategories, locationsDetails, currentLocationSysId, userConfig} = state;
			return (
				<sn-wsd-moa-services-manager
					location={locationsDetails[currentLocationSysId]}
					selectedServices={locationsServiceCategories[currentLocationSysId]}
					appointment={appointment}
					userConfig={userConfig}
				/>
			);
		}
		default:
			return (
				getLoadingIndicator(t('Loading...'))
			);
	}
}

/**
 * generate loader html
 * @param {string} msg - message to display next to indicator
 * @param {string} size - size of the indicator
 */
const getLoadingIndicator = (msg, size) => {
	size = size || 'lg';
	
	return (
		<div className="loading-wrapper">
			<now-loader label={msg} size={size}/>
		</div>
	)
}

/**
* rendering active view depend on if the appointment and office is ready
* @param {*} state
*/
const renderActiveView = (state) => {
	const {appInitSuccess} = state;

	return !appInitSuccess 
		? ( 
			<now-alert
				className="configuration-error"
				status="critical"
				icon="circle-exclamation-outline"
				content={t('Initial setup failed! One or more settings might not be configured correctly. Please contact your administrator.')}
				expanded={true}
			/>
		) 
		: ( getActiveView(state) )
}
