import { createHttpEffect } from '@servicenow/ui-effect-http';
import { ACTIONS } from '../actions';

export const getAvailability = createHttpEffect('api/sn_wsd_rsv/search/availability', {
	queryParams: ['start', 'end', 'reservable_module', 'reservable_ids', 'reservation_ids', 'shift'],
	successActionType: ACTIONS.ON_GET_AVAILABILITY_SUCCESS,
	errorActionType: ACTIONS.ON_GET_AVAILABILITY_FAILED,
	batch: false
});
