import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import { initializeOfficeHelper, getOfficeHelper } from '../office/officeHelper';
import { OfficeRestService } from '../office/officeRestService';
import { loadOffice } from '../office/officeLoader';
import { isEmpty } from 'lodash';

import { CONSTANTS } from './constants';
import { ACTIONS } from './actions';
import { actionHandlers } from './actionHandlers'
import { view } from './view';
import styles from './styles.scss';
import { convertToUTC } from '../utils/moaDateTimeUtils';
import { getWSDSensitivity } from '../office/officeUtils';


initApp();

/**
* initial function to start up the app, load officeJs and prepare component
*/
async function initApp() {
	try {
		await loadOffice();
		await initComponent();
	} catch (ex) {
		console.error('Unable to boot up Outlook Addin application', ex);
	}
}

/**
* initialize application details for this main component.
* appInitCompleted: indicates that all setting, preloading data are done (or an exception occurred)
* appInitSuccess: whether the whole initial operation succeeded (fetching setting, config etc...)
*/
async function initComponent() {
	initializeOfficeHelper(window.Office);

	const initialAppData = await prepareAppointment();

	const { appInitCompleted, appInitSuccess, appointment } = initialAppData;
	const searchRequest = {
		building: { value: null, displayValue: null },
		floors: { value: null, displayValue: [] },
		capacity: 1,
		defaultReservableModule: { value: null },
		pageSize: 8
	};

	const notificationConfig = {
		opened: false,
		headerLabel: t('Header'),
		content: t('content'),
		footerActions: [
			{
				'variant': 'primary',
				'label': t('OK')
			}
		]
	};

	// start rendering after Officejs has loaded
	createCustomElement('sn-wsd-moa-app', {
		renderer: {type: snabbdom},
		view,
		actions: ACTIONS,
		actionHandlers: actionHandlers,
		initialState: {
			searchRequest,
			appointment,
			reservation: null,
			reservables: [],
			isSyncableAppointment: false,
			appInitCompleted, // status of application startup
			appInitSuccess, // whether startup finished without any error
			notificationConfig,
			nowModalStylingApplied: false,
			activeView: CONSTANTS.VIEWS.SEARCH_FILTER,
			locationsDetails: {},
			currentLocationSysId: null,
			locationsServiceCategories: {},
			loadReservationServicesTotalCount: 0,
			loadReservationServicesCompletedCount: 0,
			loadReservationServicesFailed: false,
			enableDiagnostic: false,
			userConfig: null,
			initSearchRequestWithLocationsCompleted: false
		},
		styles
	});
}

/**
* preparing appointment data, fetches outlook client data. appInitSuccess default value is `true`
* @async
*/
async function prepareAppointment() {
	try {
		const officeHelper = getOfficeHelper();
		const officeRestService = new OfficeRestService(officeHelper);

		await officeHelper.initializeAppointment();

		const appointment = await getMeetingServerInfo(officeRestService);

		return { appInitCompleted: false, appInitSuccess: true, appointment };
	} catch (e) {
		console.warn('Exception occurred! Unable to fetch meeting data', e); // retry mechanism needed,.
		return { appInitCompleted: true, appInitSuccess: false };
	}
}

/**
* fetch appointment data from server
* @param {OfficeRestService} officeRestService
* @return {Appointment} appointment details
*/
async function getMeetingServerInfo(officeRestService) {
	const office = getOfficeHelper();
	const times = await office.getItemData('start', 'end');
	const start = convertToUTC(times[0]);
	const end = convertToUTC(times[1]);

	let appointment = {
		start: start,
		end: end,
		iCalUId: '',
		subject: await office.getSubject(),
		sensitivity: getWSDSensitivity(),
		type: CONSTANTS.RECURRENCE_TYPES.NA
	};

	try {
		const apptResponse = await officeRestService.getFullMeetingInfo();
		if (!apptResponse || !isEmpty(apptResponse.error)) {
			console.error('An error was found in attempting to load full meeting', apptResponse);
			return appointment;
		}

		const recurrence = apptResponse.Recurrence;
		const seriesMasterId = apptResponse.SeriesMasterId;
		
		appointment = {
			...appointment,
			iCalUId: apptResponse.iCalUId,
			recurrence,
			type: office.calculateAppointmentType(recurrence, seriesMasterId),
			seriesMasterId,
			sensitivity: getWSDSensitivity(apptResponse.Sensitivity)
		};
	} catch (ex) {
		console.error('Failed to fetch meeting details', ex);
	} finally {
		console.debug('Meeting appointment details', appointment);
		return appointment;
	}
}

/**
* meeting/appointment details
* @typedef Appointment
* @param {string} start - start time in UTC string
* @param {string} end - end time in UTC string
* @param {string} iCalUid - Unique ID of a meeting in Exchange
* @param {string} seriesMasterId - Unique ID of a series in Exchange in case current meeting is part of a recurring series
* @param {string} subject - subject
* @param {string} sensitivity - translated to WSD sensitivity value
*/