import { createHttpEffect } from '@servicenow/ui-effect-http';
import { ACTIONS } from '../actions';

export const loadReservation = createHttpEffect('/api/sn_wsd_rsv/reservation/get', {
	method: 'GET',
	queryParams: ['external_ical'],
	successActionType: ACTIONS.LOAD_RESERVATION_SUCCESS,
	errorActionType: ACTIONS.LOAD_RESERVATION_FAILURE,
	batch: false // doesnt work witih batching
});

export const loadReservationServiceRequests = createHttpEffect('/api/sn_wsd_rsv/wsd_extra_service_request/reservation/:reservationId', {
	method: 'GET',
	pathParams: ['reservationId'],
	successActionType: ACTIONS.LOAD_RESERVATION_SERVICE_REQUESTS_SUCCESS,
	errorActionType: ACTIONS.LOAD_RESERVATION_SERVICE_REQUESTS_FAILURE,
});