import { t } from 'sn-translate';
import '@servicenow/now-text-link';
import '@servicenow/now-button';
import '@servicenow/now-heading';

import '../sn-wsd-moa-services-summary';
import '../sn-wsd-moa-price-label';
import { ACTIONS } from './actions';
import _isEmpty from 'lodash/isEmpty';
import * as utils from '../utils/generalUtils';

export const view = (state, { dispatch }) => {
	const {properties: {extraServiceRequests, locationDetails, reservable, disableServices}} = state;

	const item = resolveReservableForView(locationDetails, reservable);
	const subtotalDetails = constructSubtotalDetails(extraServiceRequests);
	const disabledClass = !item.isAvailable ? 'disable-card' : '';

	const serviceCategoriesKeys = Object.keys(extraServiceRequests);
	const reservableHasServices = serviceCategoriesKeys.length > 0 && serviceCategoriesKeys.some(serviceCategoriesKey =>
		extraServiceRequests[serviceCategoriesKey].addedServices.some(extraServiceRequest => !extraServiceRequest.cancelFlag)
	);

	return (
		<div className="sn-wsd-moa-space-summary-card"> 
			<div className="header">
				<div className="space-name">
					<now-heading
						className="title"
						label={item.name}
						variant="title-primary"
					/>
					<now-text-link
						className="link"
						label={t('Remove')}
						href="javascript:void(0)"
						on-click={ () => dispatch(ACTIONS.RESERVABLE_SELECTED, {
							...reservable,
							is_selected: 'false'
						}) }
						configAria={{"aria-label": t('Remove') + ' ' + item.name}}/>
				</div>
				{
					item.isAvailable ? '' :	
					<div className="space-availablity">
						<now-highlighted-value label={item.availablityLabel} color="critical" show-icon></now-highlighted-value>
					</div>
				}
				<div className={`space-details ${disabledClass}`}>
					<div>{t('Seats')} {item.capacity}</div>
					<div>{item.description}</div>
				</div>
			</div>
			<div className={disabledClass}>
				{
					// all added service requests grouped by categories
					_isEmpty(extraServiceRequests) || !locationDetails ? '' : (
						Object.entries(extraServiceRequests).map(([categoryId, spaceCategoryExtraServiceRequests]) => (
							<div className="added-services">
								<sn-wsd-moa-services-summary
									serviceCategory={locationDetails.flexible_services.find(flexibleServiceCategory => flexibleServiceCategory.category === categoryId)}
									serviceRequests={spaceCategoryExtraServiceRequests}
								/>
							</div>
						))
					)
				}
				{
					// add extra services action block
					!item.hasServices ? '' :
					<div className={`${reservableHasServices ? 'has-services' : ''} actions`}>
					{
						disableServices || !item.isAvailable ? getUnavailableViewForServiceAction(item, disableServices) : (
							<now-button-bare
								size="sm"
								variant="secondary"
								iconStart="plus-outline"
								label={reservableHasServices ? t('Add/manage extra services') : t('Add extra services')}
								configAria={{'aria-label': reservableHasServices ? t('Update extra services for {0}', item.name) : t('Add extra services for {0}', item.name)}}
								on-click={() => dispatch(ACTIONS.SELECT_LOCATION_FOR_EDITING_SERVICES, { currentLocationSysId: locationDetails.sys_id })}
								hidePadding={true}
							/>
						)
					}
					</div>
				}
				{
					_isEmpty(subtotalDetails) || !reservableHasServices ? '' : (
						<sn-wsd-moa-price-label
							className="subtotal"
							mode="large"
							label={t('Subtotal')}
							value={`${subtotalDetails.currencyCode} ${subtotalDetails.price.toFixed(2)}`}
						/>
					)
				}
			</div>
		</div>
	);
}

/**
 * Construct the subtotal details for all categories
 * @param {Object}
 * @return {{price: string, currencyCode: string}}
 */
function constructSubtotalDetails(extraServiceRequest) {
	if (_isEmpty(extraServiceRequest))
		return null;

	const categoryNames = Object.keys(extraServiceRequest);
	if (categoryNames.length === 0)
		return null;

	return categoryNames.reduce((accumulator, current) => {
		const category = extraServiceRequest[current];
		accumulator.price += category.totalPrice;

		if (!accumulator.currencyCode)
			accumulator.currencyCode = category.categoryCurrencyCode;

		return accumulator;
	}, {
		price: 0,
		currencyCode: null
	});
}

/**
 * get view of the action block of unavailable item
 * @param {ResolvedReservable} item 
 * @returns {string}
 */
 const getUnavailableViewForServiceAction = (item, reasonIsRecurring) => {
	 if (reasonIsRecurring) {
		 return <i>{t('Service request is not available for recurring reservation')}</i>;
	 }

	if (!item.hasServices) 
		return '';

	return <i>{t('Service request is not possible due to unavailablity')}</i>;
}

/**
 * Resolve reservable into an object that can be directly consumed by the front-end
 * @return {ResolvedReservable} a view model item
 */
const resolveReservableForView = (locationDetails, reservable) => {
	if(_isEmpty(locationDetails)) {
		locationDetails = { 
			existsInWsd: false,
			name: reservable.name,
			capacity: 0, 
			details: '',
			isAvailable: false,
			flexible_services: []
		};
	} else {
		locationDetails.existsInWsd = true;
	}

	const isAvailable = locationDetails.existsInWsd && reservable.is_available;
	const availablityLabel = !locationDetails.existsInWsd
		? t('Not recognized in WSD') 
		: (isAvailable ? '' : t('Unavailable'));

	return {
		name: locationDetails.name,
		capacity: locationDetails.capacity,
		description: utils.constructLocationDescription(locationDetails),
		isAvailable: isAvailable,
		hasServices: utils.isArrayHasElement(locationDetails.flexible_services),
		existsInWsd: locationDetails.existsInWsd,
		availablityLabel: availablityLabel
	}
}

/**
 * @typedef ResolvedReservable
 * @property {string} name
 * @property {number} capacity - 0 if unknown
 * @property {string} details - description 
 * @property {boolean} isAvailable
 * @property {boolean} hasServices - whether services can be added to the item
 */