import { t } from 'sn-translate';
import '@servicenow/now-button';

import '../sn-wsd-moa-price-label';

export const view = (state, {updateState}) => {
	const {
		properties: {
			serviceCategory: {
				label,
				currency_code,
			},
			serviceRequests: {
				totalPrice,
				totalQuantity,
				addedServices,
			}
		},
		isOpen,
		servicesSummary,
	} = state;

	if (addedServices.length === 0 || !addedServices.some(addedService => !addedService.cancelFlag))
		return null;

	return (
		<div className={`sn-wsd-moa-services-summary ${isOpen ? 'is-open' : ''}`}>
			<div className="header">
				<now-button-iconic
					bare
					size="sm"
					variant="tertiary"
					icon={`chevron-${isOpen ? 'down' : 'right'}-fill`}
					on-click={() => {updateState({ isOpen: !isOpen })}}
					hidePadding={true}
				/>
				<sn-wsd-moa-price-label
					title={t('{0} ({1}) - {2} {3}', [label, totalQuantity, currency_code, totalPrice.toFixed(2)])}
					label={`${label} (${totalQuantity})`}
					value={isOpen ? '' : `${currency_code} ${totalPrice.toFixed(2)}`}
					className="header-text"
				/>
			</div>
			<div className="body display">
				{!isOpen ? '' : 
					Object.entries(servicesSummary).map(([subCategoryId, subCategory]) => (
						<div key={subCategoryId} className={'sub-category'}>
							<sn-wsd-moa-price-label
								title={t('({0}) {1}: {2} - {3} {4}', [subCategory.totalQuantity, subCategory.label, subCategory.servicesLabels.join(', '), currency_code, subCategory.totalPrice.toFixed(2)])}
								label={`(${subCategory.totalQuantity}) ${subCategory.label}: ${subCategory.servicesLabels.join(', ')}`}
								value={`${currency_code} ${subCategory.totalPrice.toFixed(2)}`}
							/>
						</div>
					))
				}
			</div>
		</div>
	);
}
