/**
 * Add custom styling to now-modal child elements (now-modal, now-modal-dialog)
 * Because of the small viewport, now-modal will enforce mobile styling. It will take up the entire size of the element
 * To ensure that the styling is similar to desktop, we find the elements in the shadow root and apply the necessary styles inline
 * @param {*} host
 * @return {boolean}
 */
export function applyDefaultNowModalStyles(host) {
	const nowModalComponent = host.shadowRoot.querySelector('now-modal');
	if (!nowModalComponent)
		return false;

	const {shadowRoot: nowModalShadowRoot} = nowModalComponent;
	const nowModal = nowModalShadowRoot.querySelector('.now-modal');
	const nowModalDialog = nowModalShadowRoot.querySelector('.now-modal-dialog');
	if (!nowModal || !nowModalDialog)
		return false;

	const nowModalHeading = nowModalShadowRoot.querySelector('.now-modal-heading');
	const nowModalBody = nowModalShadowRoot.querySelector('.now-modal-body');
	const nowModalFooterButton = nowModalShadowRoot.querySelector('.now-modal-footer-button');
	if (!nowModalHeading || !nowModalBody || !nowModalFooterButton)
		return false;

	const nowButton = nowModalFooterButton.shadowRoot.querySelector('.now-button');
	if (!nowButton)
		return false;

	Object.assign(nowModal.style, {display: 'flex', alignItems: 'center', justifyContent: 'center'});
	Object.assign(nowModalDialog.style, {width: '90%', height: 'auto'});
	Object.assign(nowModalHeading.style, {fontSize: '18px'});
	Object.assign(nowModalBody.style, {fontSize: '14px'});
	Object.assign(nowButton.style, {fontSize: '14px'});

	return true;
}
