import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';
import { cloneDeep, find, isEmpty, map } from 'lodash';

import { getOfficeHelper } from '../office/officeHelper';
import { getSettings } from './effects/settingEffect';
import { getAvailability } from './effects/availabilityEffect';
import { getLocationsDetails } from './effects/locationEffects';
import { loadReservation, loadReservationServiceRequests } from './effects/loadReservationEffects';
import { ACTIONS } from './actions';
import * as utils from '../utils/generalUtils';
import { applyDefaultNowModalStyles } from '../utils/htmlUtils';
import { convertToUTC } from '../utils/moaDateTimeUtils';
import { CONSTANTS } from './constants';
import MultiEffectStateWrapper from '../utils/multiEffectStateWrapper';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_RENDERED} = actionTypes;

const multiEffectStateWrapper = new MultiEffectStateWrapper({
	totalCountKey: 'loadReservationServicesTotalCount',
	completedCountKey: 'loadReservationServicesCompletedCount',
	isFailedKey: 'loadReservationServicesFailed',
	successAction: ACTIONS.LOAD_FULL_RESERVATION_COMPLETED,
	failureAction: ACTIONS.LOAD_FULL_RESERVATION_FAILURE
});

let _initialCompleted = false;

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: async ({state, dispatch}) => {
		const { appInitCompleted } = state; // incase office initialization failed, the appInitCompleted will be set to true, and prevent reload config

		if (appInitCompleted)
			return;

		dispatch(ACTIONS.LOAD_SETTINGS);
	},

	[COMPONENT_RENDERED]: ({action, state, updateState}) => {
		const {nowModalStylingApplied} = state;
		const {payload: {host}} = action;

		if (nowModalStylingApplied)
			return;

		// styling is applied within the render lifecycle method, because not all <now-modal> elements exist when the component is bootstrapped
		const result = applyDefaultNowModalStyles(host);
		if (!result)
			return;

		updateState({
			...state,
			nowModalStylingApplied: true
		});
	},

	[ACTIONS.LOAD_SETTINGS]: getSettings,

	[ACTIONS.ON_LOAD_SETTINGS_SUCCESS]: async ({action, state, updateState, dispatch}) => {
		const {payload: {result}} = action;
		const {
			default_reservable_module: defaultReservableModuleSysId,
			init_search_config: initSearchConfig,
			enable_diagnostic: enableDiagnostic,
			user_config: userConfig,
			page_size: pageSize
		} = result;
		let newReservables = [];

		if (!defaultReservableModuleSysId) {
			_completeIntialSetup([], true, false, updateState);

			console.error('Intialization failed. Terminate');
			return;
		}

		dispatch(ACTIONS.SETTINGS_LOADED, result);

		// sync with Outlook's enhanced locations, if there is any location added, fetch data and prepare to switch to Summary view
		const officeHelper = getOfficeHelper();

		let enhancedLocations = await officeHelper.getItemData('enhancedLocation');
		if (!enhancedLocations)
			enhancedLocations = [];

		updateState(({state}) => {
			const { appointment, reservables } = state;
			// setting is ready fetched from server with proper reservable module
			const searchConfigResult = _fetchSearchConfig(initSearchConfig);
			const {building, floors} = searchConfigResult;

			let newState = {
				...state,
				searchRequest: {
					...state.searchRequest, 
					defaultReservableModule: {
						value: defaultReservableModuleSysId 
					},
					building,
					floors,
					pageSize
				},
				enableDiagnostic,
				userConfig
			};
			
			if (utils.isArrayHasElement(enhancedLocations)) {
				newReservables = _mergeReservables(reservables, enhancedLocations);

				newState = {
					...newState,
					reservables: [...newReservables]
				}
			} 

			const isSyncableAppointment = appointment && !isEmpty(appointment.iCalUId); 
			newState.isSyncableAppointment = isSyncableAppointment;

			_setupSubscribers(dispatch, false);

			if (utils.isArrayHasElement(newState.reservables)) {
				// retrieving full details for reservables. When applicable, also update search request to match, resevable building
				dispatch(ACTIONS.TRIGGER_LOAD_LOCATIONS_DETAILS);
			}
			else {
				// when there are no reservables, no need to update SearchRequest
				newState.initSearchRequestWithLocationsCompleted = true;
			}

			if (!isSyncableAppointment)
				return _completeIntialSetup(newReservables, true, true, null, newState);

			// final action of intial load, and up on full reservation loaded (including services), set Init Completed to true.
			dispatch(ACTIONS.TRIGGER_LOAD_RESERVATION);

			return newState;
		});
	},

	[ACTIONS.ON_LOAD_SETTINGS_FAILED]: ({action, state, updateState, dispatch}) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;

		if (utils.isRequestUnauthorized(statusCode, statusText)) {
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});
			return;
		}

		updateState({
			...state,
			appInitCompleted: true,
			appInitSuccess: false
		});
	},

	[ACTIONS.ON_TIME_CHANGED]: ({action, state, updateState, dispatch}) => {
		const {reservables, reservation, searchRequest: {defaultReservableModule: {value: reservable_module}}} = state;
		const {payload: {start, end}} = action;

		updateState({
			...state,
			appointment: {
				...state.appointment,
				start,
				end
			}
		});

		_refreshAvailablities(reservables, reservable_module, start, end, reservation, false, dispatch);
	},

	[ACTIONS.RESET_LOADED_RESERVABLES]: ({updateState}) => {
		updateState(({state}) => ({
			...state,
			reservables: state.reservables.filter((reservable) => {
				return utils.safeBool(reservable.is_selected);
			})
		}));
	},

	[ACTIONS.RESERVABLES_PAGE_LOADED]: ({action, updateState}) => {
		const {reservables: payloadReservables, error} = action.payload;

		updateState(({state}) => {
			const oldReservables = Array.isArray(state.reservables) ? state.reservables : [];
			let selectedReservables = oldReservables.filter((reservable) => {
				return utils.safeBool(reservable.is_selected);
			});

			const selectedReservableEmails = selectedReservables.map((r) => {
				return r.email;
			});

			// prepare current selected reservable
			selectedReservables = _fetchSearchDataToReservables(selectedReservables, payloadReservables);

			const tobeAddedReservables = payloadReservables.filter((r) => {
				return !selectedReservableEmails.some(selectedReservableEmail => utils.isEqual(selectedReservableEmail, r.email, true));
			});

			// merge the new selectedReservables with oldReservables
			let newReservables = oldReservables.map(reservable => {
				const foundSelectedReservable = selectedReservables.find(selectedReservable => utils.isEqual(selectedReservable.email, reservable.email, true))
				return foundSelectedReservable ? foundSelectedReservable : reservable;
			});

			newReservables = newReservables.concat(tobeAddedReservables);
			
			return {
				...state,
				reservables: newReservables
			};
		});
	},

	[ACTIONS.RESERVABLE_SELECTED]: ({action, state, updateState}) => {
		const reservable = action.payload;
		const { reservables } = state;
		const index = reservables.findIndex(item => utils.isEqual(item.email, reservable.email, true));

		if (index !== -1) {
			const officeHelper = getOfficeHelper();
			let activeView = state.activeView;

			if (utils.safeBool(reservable.is_selected))
				officeHelper.addRoomToMeeting(reservable.name, reservable.email);
			else
				officeHelper.removeRoomFromMeeting(reservable.name, reservable.email);

			reservables[index] = reservable; // when this action handler is called, the reservable is already modified so it is shallow cloned already.

			updateState({
				...state,
				reservables: [...reservables],
				activeView
			});
		}
	},

	[ACTIONS.ON_ENHANCED_LOCATIONS_CHANGED]: ({action, state, updateState, dispatch}) => {
		let { payload: {locations} } = action;
		updateState(({state}) =>  {
			const { reservables, locationsDetails } = state;
			const newReservables = _mergeReservables(reservables, locations, locationsDetails);
			let newState = {
				...state,
				reservables: [...newReservables]
			};

			return newState;
		});

		dispatch(ACTIONS.TRIGGER_LOAD_LOCATIONS_DETAILS);
	},

	[ACTIONS.ON_RECURRENCE_CHANGED]: ({action, updateState, state}) => {
		let newState = {
			...state,
			appointment: {
				...state.appointment,
				type: CONSTANTS.RECURRENCE_TYPES.MASTER,
				recurrence: action.payload.recurrence
			}
		};

		if (state.activeView === CONSTANTS.VIEWS.SERVICES_MANAGER)
			newState.activeView = CONSTANTS.VIEWS.SUMMARY;

		updateState(newState);
	},

	[ACTIONS.TRIGGER_REFRESH_AVAILABLITIES]: ({action, state, dispatch}) => {
		const { payload: { onlySelectedItems } } = action;

		_refreshAvailablitiesUsingState(state, onlySelectedItems, dispatch);
	},

	[ACTIONS.GET_AVAILABILITY]: getAvailability,

	[ACTIONS.ON_GET_AVAILABILITY_SUCCESS]: ({action, updateState}) => {
		const {payload: {result}} = action;

		if (!utils.isArrayHasElement(result)) {
			if (!_initialCompleted)
				_completeIntialSetup(null, true, true, updateState);

			return;
		}

		updateState(({state}) => {
			const { reservables } = state;
			let newReservables = [...reservables];
			for (let i = 0; i < result.length; i++) {
				const reservable = result[i];
				const reservableIdx = newReservables.findIndex(item => item.sys_id === reservable.sys_id || item.email === reservable.email);

				if (reservableIdx === -1)
					continue;

				newReservables[reservableIdx] = {
					...newReservables[reservableIdx],
					is_available: reservable.is_available
				};
			}

			// for intial load, return the new completed initial state
			if (!_initialCompleted)
				return _completeIntialSetup(newReservables, true, true, null, state);

			// otherwise return new state with new reservables with fetched availablity
			return{
				...state,
				reservables: newReservables
			};
		});
	},

	[ACTIONS.ON_GET_AVAILABILITY_FAILED]: ({state, action, updateState, dispatch}) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;
		console.error('Error! Unable to get availablity', error.message);

		if (utils.isRequestUnauthorized(statusCode, statusText)) {
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});
			return;
		}
		
		if (!_initialCompleted) {
			_completeIntialSetup(null, true, true, updateState);
			return;
		}
	},

	[ACTIONS.TRIGGER_NAVIGATION]: ({action, state, updateState}) => {
		const {view: activeView} = action.payload;
		let newState = {
			...state,
			activeView
		};

		updateState(newState);
	},

	[ACTIONS.TRIGGER_LOAD_LOCATIONS_DETAILS]: ({ dispatch, state }) => {
		const {
			reservables,
			searchRequest,			
			locationsDetails
		} = state;

		// include emails in the list if they dont have sys_ids. Since they wont match any ids, they will go to the locationDetailsToLoad
		const selectedReservableIdsArr = reservables.reduce((reservableIdsArr, reservable) => {
			if (reservable.is_selected)
				reservableIdsArr.push(reservable.sys_id || reservable.email);

			return reservableIdsArr;
		}, []);

		const locationDetailsIds = locationsDetails ? Object.keys(locationsDetails) : [];

		const locationDetailsToLoad = selectedReservableIdsArr.filter(selectedReservableId => !locationDetailsIds.includes(selectedReservableId));

		if (locationDetailsToLoad.length === 0)
			return;

		dispatch(ACTIONS.LOAD_LOCATIONS_DETAILS, {
			ids: locationDetailsToLoad.join(','),
			reservable_module_sys_id: searchRequest.defaultReservableModule.value,
			include_flexible_services: true,
			include_standard_services: true,
			include_reservable_purposes: true,
			include_layouts: false
		});
	},

	[ACTIONS.LOAD_LOCATIONS_DETAILS]: getLocationsDetails,

	[ACTIONS.ON_LOAD_LOCATIONS_DETAILS_SUCCESS] : ({ action, updateState, state }) => {
		const locationsDetailsPayload = action.payload.result || [];
		const locationsDetails = {...state.locationsDetails};
		locationsDetailsPayload.forEach(locationDetails => locationsDetails[locationDetails.sys_id] = locationDetails);

		updateState(({state}) => {
			let { reservables } = state; 
			reservables = reservables.map(reservable => {
				if (reservable.sys_id)
					return reservable;
	
				const locDetail = _getLocationDetailsByEmail(locationsDetails, reservable.email);
				if (!locDetail)
					return reservable;
	
				return { 
					...reservable,
					existsInWsd: true,
					name: locDetail.name,
					sys_id: locDetail.sys_id 
				};
			})

			const newState = { 
				...state,
				reservables: [...reservables],
				locationsDetails
			};

			if (!newState.initSearchRequestWithLocationsCompleted) {
				const updateSearchRequestObj = _getSearchRequestBySelectedReservables(newState.reservables, newState.locationsDetails);
				if (updateSearchRequestObj) {
					newState.searchRequest = {
						...newState.searchRequest,
						...updateSearchRequestObj
					};
				}

				newState.initSearchRequestWithLocationsCompleted = true;
			}

			return newState;
		})
	},

	[ACTIONS.ON_LOAD_LOCATIONS_DETAILS_FAILURE] : ({action, dispatch}) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;

		if (utils.isRequestUnauthorized(statusCode, statusText))
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});

		console.error('location details failure: ', action.payload);
	},

	[ACTIONS.SELECT_LOCATION_FOR_EDITING_SERVICES]: ({ action, updateState, state }) => {
		const currentLocationSysId =  action.payload.currentLocationSysId;
		const extraServiceRequestsForSingleLocation = state.locationsServiceCategories[currentLocationSysId] || {};

		updateState({
			...state,
			currentLocationSysId,
			activeView: CONSTANTS.VIEWS.SERVICES_MANAGER,
			locationsServiceCategories: {
				...state.locationsServiceCategories,
				[currentLocationSysId]: extraServiceRequestsForSingleLocation
			}
		});
	},

	[ACTIONS.ON_SAVE_RESERVABLES]: ({ updateState, state }) => {
		updateState({
			...state,
			activeView: CONSTANTS.VIEWS.SUMMARY
		});
	},

	[ACTIONS.UPDATE_SERVICE_REQUESTS]: ({ updateState, state, action }) => {
		const { locationId, locationExtraServiceRequests } = action.payload;

		updateState({
			...state,
			activeView: CONSTANTS.VIEWS.SUMMARY,
			locationsServiceCategories: {
				...state.locationsServiceCategories,
				[locationId]: locationExtraServiceRequests,
			}
		});
	},

	[ACTIONS.NOW_MODAL_OPENED_SET]: ({dispatch}) => {
		dispatch(ACTIONS.CLOSE_NOW_MODAL);
	},

	[ACTIONS.NOW_MODAL_FOOTER_ACTION_CLICKED]: ({action, dispatch}) => {
		const {payload: {footerAction}} = action;

		if (footerAction.status === 'unauthorized') {
			window.location.href = `${window.location.origin}/ws?id=moa_login`;
			return;
		}

		if (footerAction.variant === 'primary')
			dispatch(ACTIONS.CLOSE_NOW_MODAL);
	},

	[ACTIONS.OPEN_NOW_MODAL]: ({action, state, updateState}) => {
		const {payload: {headerLabel, content, footerActions}} = action;
		let notificationConfig = {
			...state.notificationConfig,
			opened: true,
			headerLabel: headerLabel || t('Something went wrong.'),
			content: content || t('An error has occurred. Please, try again.')
		};

		if (footerActions)
			notificationConfig.footerActions = footerActions;

		updateState({
			...state,
			notificationConfig
		});
	},

	[ACTIONS.CLOSE_NOW_MODAL]: ({state, updateState}) => {
		updateState({
			...state,
			notificationConfig: {
				...state.notificationConfig,
				opened: false
			}
		});
	},
	
	[ACTIONS.CLOSE_APP]: () => {
		getOfficeHelper().closeAddin();
	},

	[ACTIONS.LOAD_FULL_RESERVATION_COMPLETED]: async ({ updateState, state, dispatch }) => {
		const { reservables } = state;
		console.debug('Rsv completed! Reservation and all related reserved services loaded');

		if (!utils.isArrayHasElement(reservables)) {
			_completeIntialSetup(null, true, true, updateState);
			return;
		}

		if (!_initialCompleted) {
			// at the very end of the chain: trigger refresh availablities, at this point, all reservation data are available to avoid collision with original meeting
			dispatch(ACTIONS.TRIGGER_REFRESH_AVAILABLITIES, { onlySelectedItems: true });
		}
	},

	[ACTIONS.LOAD_FULL_RESERVATION_FAILURE]: ({ updateState, state, dispatch }) => {
		console.error('ERROR! Failed to load reservation and all related reserved services. Ignore and continue with intial flow');

		if (!utils.isArrayHasElement(reservables)) {
			_completeIntialSetup(null, true, true, updateState);
			return;
		}

		if (!_initialCompleted) {
			// at the very end of the chain: trigger refresh availablities. There are will be no reserved reservation to check against.
			dispatch(ACTIONS.TRIGGER_REFRESH_AVAILABLITIES, { onlySelectedItems: true });
		}
	},

	[ACTIONS.TRIGGER_LOAD_RESERVATION]: ({ state, dispatch, updateState }) => {
		updateState(({state}) => {
			const {
				appointment: {
					iCalUId: external_ical
				}
			} = state;
	
			let newState = {
				...state,
				reservation: null,
				locationsServiceCategories: {}
			};

			newState = multiEffectStateWrapper.reset(newState);
	
			dispatch(ACTIONS.LOAD_RESERVATION, {
				external_ical
			});

			return newState;
		});
	},

	[ACTIONS.LOAD_RESERVATION]: loadReservation,

	[ACTIONS.LOAD_RESERVATION_SUCCESS]: ({ action, dispatch, updateState, state }) => {
		const reservation = action.payload.result.reservation;
		const reservedLocations = reservation.locations || [reservation.location];

		if (!reservation.active) {
			dispatch(ACTIONS.LOAD_FULL_RESERVATION_COMPLETED);
			return;
		}

		updateState(({state}) => {
			const { reservables } = state;
			const newReservables = [...reservables];
			if (utils.isArrayHasElement(newReservables)) {
				newReservables.forEach((reservable, index) => {
					const matchedReservedReservable = reservedLocations.find(rl => utils.isEqual(rl.email, reservable.email, true));
					if (matchedReservedReservable) {
						newReservables[index] = cloneDeep(reservable);
						newReservables[index].is_reserved = true;
						newReservables[index].is_available = true;
					}
				});
			}

			return {
				...state,
				loadReservationServicesTotalCount: reservedLocations.length,
				reservation,
				reservables: newReservables
			};
		});

		_loadServiceRequests(dispatch, reservedLocations);
	},

	[ACTIONS.LOAD_RESERVATION_FAILURE]: ({action, dispatch}) => {
		const {payload} = action;

		if (utils.isRequestUnauthorized(payload.status, payload.statusText)) {
			console.error('User not authorized', payload);
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error: payload.data.error});
			return;
		}

		if (payload.status === 404) {
			console.error('No reservation found', payload);
			dispatch(ACTIONS.LOAD_FULL_RESERVATION_COMPLETED);
			return;
		}

		console.error('Reservation failed', payload);
		dispatch(ACTIONS.LOAD_FULL_RESERVATION_FAILURE);
	},

	[ACTIONS.LOAD_RESERVATION_SERVICE_REQUESTS]: loadReservationServiceRequests,

	[ACTIONS.LOAD_RESERVATION_SERVICE_REQUESTS_SUCCESS]: ({ action, updateState, dispatch }) => {
		updateState(({state}) => {
			const {
				reservableId,
				serviceCategories = {}
			} = action.payload.result;

			const locationServiceCategories = utils.mapObject(serviceCategories, (categoryId, serviceCategory) => {
				let {
					flexibleServices: addedServices,
					...locationServiceCategory
				} = serviceCategory;

				addedServices.forEach(addedService => {
					addedService.clientId = utils.generateGUID();
				});
				
				locationServiceCategory.addedServices = addedServices;
				locationServiceCategory.totalQuantity = utils.calculateServiceCategoryQuantity(addedServices);
				locationServiceCategory.totalPrice = utils.calculateServiceCategoryPrice(addedServices);
				return locationServiceCategory;
			});

			let newState = {
				...state,
				locationsServiceCategories: {
					...state.locationsServiceCategories,
					[reservableId]: locationServiceCategories
				}
			};
			console.debug('Loaded service requests: ', action.payload.result);

			// dispatches LOAD_FULL_RESERVATION_COMPLETED or LOAD_FULL_RESERVATION_FAILURE when all services complete
			return multiEffectStateWrapper.handleRequestComplete(newState, dispatch, true);
		});
	},

	[ACTIONS.LOAD_RESERVATION_SERVICE_REQUESTS_FAILURE]: ({ updateState, dispatch, action }) => {
		const {
			payload: {
				data: { error },
				status: statusCode,
				statusText
			}
		} = action;

		if (utils.isRequestUnauthorized(statusCode, statusText)) {
			dispatch(ACTIONS.TRIGGER_UNAUTHORIZED_MODAL, {error});
			return;
		}
			
		updateState(({state}) => {
			console.error('Failed to load service requests: ', action.payload);

			// dispatches LOAD_FULL_RESERVATION_FAILURE when all services complete
			return multiEffectStateWrapper.handleRequestComplete({...state}, dispatch, false);
		});
	},

	[ACTIONS.UPDATE_SEARCH_REQUEST]: ({action, state, updateState}) => {
		const {building, floors, capacity} = action.payload;

		const newSearchRequest = {
			...state.searchRequest,
			building,
			floors,
			capacity
		}
		updateState({
			...state,
			searchRequest: newSearchRequest
		})
	},

	[ACTIONS.TRIGGER_UNAUTHORIZED_MODAL]: ({action, dispatch}) => {
		const {payload: {error}} = action;

		dispatch(ACTIONS.OPEN_NOW_MODAL, {
			headerLabel: t('Unauthorized'),
			content: error.message,
			footerActions: [
				{
					'variant': 'primary',
					'label': t('Login'),
					'status': 'unauthorized'
				}
			]
		});
	}
}

/**
 * setup all subscribers to listen to outlook meeting details' changes
 * @param {function} dispatch 
 * @param {boolean} requireRefreshEnhancedLocation - when OfficeJs failed to retrieve enhancedLocation on getAsync() (randomly)
 */
async function _setupSubscribers(dispatch, requireRefreshEnhancedLocation) {
	const officeHelper = getOfficeHelper();

	officeHelper.subscribeTimeChanges((res) => {
		if (res.status !== 'success') {
			console.error('Subscribed failed. Can not retrieve appointment times');
			return;
		}

		const start = convertToUTC(res.data.start);
		const end = convertToUTC(res.data.end);

		dispatch(ACTIONS.ON_TIME_CHANGED, { start, end });
	});

	officeHelper.subscribeEnhancedLocationChanges((res) => {
		if (res.status !== 'success') {
			console.error('Subscribed failed. Cannot retrieve location information');
			return;
		}

		const {data: { enhancedLocations: locations }} = res;
		dispatch(ACTIONS.ON_ENHANCED_LOCATIONS_CHANGED, { locations });
	});

	officeHelper.subscribeRecurrenceChanged((rec) => {
		if (rec.status !== 'success') {
			console.error('Subscribed failed. Cannot retrieve location information');
			return;
		}

		const {data: { recurrence }} = rec;
		dispatch(ACTIONS.ON_RECURRENCE_CHANGED, { recurrence });
	});
}

/**
 * Fetch the search config values
 * @param {Object} config
 * @return {Object}
 */
function _fetchSearchConfig(config) {	
	if (!config) {
		return {
			building: {value: null, displayValue: null},
			floors: {value: null, displayValue: []}
		};
	}

	const {building: configBuilding, floors: configFloor} = config;

	const building = configBuilding
		? {value: configBuilding.sys_id, displayValue: configBuilding.display_value}
		: {value: null, displayValue: null};

	const floors = configFloor && utils.isArrayHasElement(configFloor)
		? {value: configFloor.map(el => el.sys_id).join(','), displayValue: configFloor.map(el => el.display_value)}
		: {value: null, displayValue: []};

	return {
		building,
		floors
	};
}

/**
 * For each location, it triggers the loading of its services, by that locations reservation id.
 * @param {*} dispatch
 * @param {Array} locations
 */
function _loadServiceRequests(dispatch, locations) {
	locations.forEach(location => {
		dispatch(ACTIONS.LOAD_RESERVATION_SERVICE_REQUESTS, {
			reservationId: location.reservation.sys_id
		});
	})
}

/**
 * get location details of the provided email based on the loaded locationsDetails
 * @param {*} locationsDetails - dictionary of all locations details for lookup
 * @param {string} email 
 * @returns {*} a single location details object
 */
function _getLocationDetailsByEmail(locationsDetails, email) {
	if (isEmpty(locationsDetails) || !email)
		return null;

	for (var prop in locationsDetails) {
		if (locationsDetails.hasOwnProperty(prop) && utils.isEqual(locationsDetails[prop].email, email, true)) 
			return locationsDetails[prop];
	}

	return null;
}

/**
 * merge reservables (search result) and enhanced locations (from Outlook Client) into a new reservables list.
 * @param {Reservable[]} reservables - list or reservable (used by search result)
 * @param {EnhancedLocation[]} enhancedLocations - list of enhanced locations fetched from outlook via officeJs
 * @param {LocationsDetails} [locationsDetails] - list of already fetched (server) locations
 * @returns {Reservable[]} deep-cloned reservables and merged with endhanced location
 */
function _mergeReservables(reservables, enhancedLocations, locationsDetails) {
	reservables = Array.isArray(reservables) ? cloneDeep(reservables) : [];

	// select|deselect a reservable based on the 'enhancedLocation' result
	reservables.forEach((reservable) => {
		if (!enhancedLocations) {
			reservable.is_selected = false;
			return;
		}

		const locationIdx = enhancedLocations.findIndex(loc => utils.isEqual(loc.emailAddress, reservable.email, true));
		reservable.is_selected = locationIdx !== -1;
	});

	// add resources of type 'room' from 'enhancedLocation'
	let tobeAddedReservables = [];
	if (Array.isArray(enhancedLocations)) {
		enhancedLocations.forEach((loc) => {
			const {displayName: name, emailAddress: email, locationIdentifier: {type: type}} = loc;

			if (type !== 'room')
				return;

			const reservableIdx = reservables.findIndex(reservable => utils.isEqual(reservable.email, email, true));
			if (reservableIdx !== -1)
				return;

			const locationDetails = _getLocationDetailsByEmail(locationsDetails, email);
			tobeAddedReservables.push({
				is_available: true,
				is_selected: true,
				fromOutlook: true,
				name,
				email,
				sys_id: locationDetails ? locationDetails.sys_id : null
			});
		});
	}

	reservables = reservables.concat(tobeAddedReservables);
	return reservables;
}

/**
 * fetch data from the search result to the current reservables (from previous search or from initial load)
 * @param {Reservable[]} selectedReservables - current selected reservables (might be fetched from Enhanced Location and previous search)
 * @param {Reservable[]} resultReservables - list of new reservable with full data (from search result)
 * @returns 
 */
function _fetchSearchDataToReservables(selectedReservables, resultReservables) {
	selectedReservables = Array.isArray(selectedReservables) ? selectedReservables : []
	const resolvedReservable = selectedReservables.map(selectedReservable => {
		const matchedResult = resultReservables.find(r => utils.isEqual(selectedReservable.email, r.email));

		// if matchedRsult found, use that instead
		return !matchedResult 
			? selectedReservable
			: {
				...matchedResult,
				is_selected: true,
				fromOutlook: true,
				existsInWsd: true
			}
	});

	return resolvedReservable;
};

/**
 * send request to get availablities of all reservables or the selected reservable (using state values)
 * @param {*} state 
 * @param {boolean} onlySelectedItems 
 * @param {*} dispatch 
 */
function _refreshAvailablitiesUsingState(state, onlySelectedItems, dispatch) {
	const {reservables, searchRequest: {defaultReservableModule: {value: reservableModule}}, appointment: { start, end }, reservation} = state;
	
	_refreshAvailablities(reservables, reservableModule, start, end, reservation, onlySelectedItems, dispatch);
}

/**
 * send request to get availablities of all reservables or the selected reservable
 * @param {Reservable[]} reservables 
 * @param {string} reservableModule - sysId of the reservable module
 * @param {string} start - meeting start in UTC
 * @param {string} end - meeting end in UTC
 * @param {Reservation} reservation - reservation if was previously submitted
 * @param {boolean} onlySelectedItems - whether to evaluate only the selected items
 * @param {*} dispatch 
 * @returns 
 */
function _refreshAvailablities(reservables, reservableModule, start, end, reservation, onlySelectedItems, dispatch) {
	console.debug('[MOA]:: start refreshing avaialblity', reservables, start, end, onlySelectedItems);
	if (!utils.isArrayHasElement(reservables))
		return;

	const tobeCheckedReservables = reservables.filter((reservable) => {
		const isValidEmail = reservable.email && reservable.email.length > 0;
		const isIncluded = onlySelectedItems ? reservable.is_selected : true;
		
		return isValidEmail && isIncluded;
	});

	let reservableIds = utils.resolveListIntoStr(tobeCheckedReservables, 'sys_id');
	if (isEmpty(reservableIds))
		reservableIds = utils.resolveListIntoStr(tobeCheckedReservables, 'email');

	if (isEmpty(reservableIds)) {
		console.error('Unable to construct reservable identities to check for availablities', tobeCheckedReservables);
		return;
	}

	const requestData = {
		start,
		end,
		reservable_module: reservableModule,
		reservable_ids: reservableIds
	};

	const reservationIds = _getReservationSysIdsFromReserved(reservation);
	if (!isEmpty(reservationIds))
		requestData.reservation_ids = reservationIds;

	dispatch(ACTIONS.GET_AVAILABILITY, requestData);
}

/**
 * constructing a string (comma separated) sys_ids of reserved reservation
 * @param {Reservation} reservation 
 * @returns {string} 
 */
function _getReservationSysIdsFromReserved(reservation) {
	if (isEmpty(reservation))
		return '';

	// multi-reservation
	if (utils.isArrayHasElement(reservation.locations)) {
		return reservation.locations.reduce((acc, loc) => { 
			if (!loc.reservation || !loc.reservation.sys_id)
				return acc;

			acc.push(loc.reservation.sys_id);
			return acc;
		}, []).join('');
	}

	// single reservation
	return reservation.sys_id;
}

/**
 * used only on initial load to complete the flow, depend on [updateState] OR [state] is given, updateState function ill be called or just simply return a new state object
 * @param {Reservable[]} reservables 
 * @param {boolean} isCompleted 
 * @param {boolean} isSuccessful 
 * @param {function|null} [updateState] - when it's given the updateState function will be executed with call back function as param.
 * @param {State|null} [state] - state object, is used and and will return a newState object without executing the updateState
 * @return {State|null} new state object (incase updateState function is not given, and state is given), null if exception occurred
 */
function _completeIntialSetup(reservables, isCompleted, isSuccessful, updateState, state) {
	_initialCompleted = true;
	try {
		if (!updateState && !state) {
			console.error('Error! Incomplete functionc call');
			return null;
		}

		// when state is given, simply return newState
		if (!isEmpty(state))
			return _getInitialFinalStateObj(state, reservables, isCompleted, isSuccessful);

		// otherwise (when updateState is given, use update state and return null)
		let newState = null;
		updateState(({state}) => {
			newState = _getInitialFinalStateObj(state, reservables, isCompleted, isSuccessful);
			return newState;
		});

		return newState;
	} catch (ex) {
		console.error('Exception occurred while completing intial load', ex);
		return null;
	}
}

/**
 * construct and return newState object based on given params
 * @param {*} state 
 * @param {*} reservables 
 * @param {*} isCompleted 
 * @param {*} isSuccessful 
 * @return {State} new state object
 */
function _getInitialFinalStateObj(state, reservables, isCompleted, isSuccessful) {
	reservables = reservables ? reservables : state.reservables;
	const activeView = utils.isArrayHasElement(reservables) ? CONSTANTS.VIEWS.SUMMARY : CONSTANTS.VIEWS.SEARCH_FILTER;

	const newState = {
		...state,
		reservables,
		appInitCompleted: isCompleted,
		appInitSuccess: isSuccessful,
		activeView
	};

	return newState;
}

/**
 * Gets the Obj to update searchResults by when there is a location which could be used.
 * @param {*} reservables
 * @param {*} locationsDetails
 * @returns {*}
 */
function _getSearchRequestBySelectedReservables(reservables, locationsDetails) {
	if (isEmpty(reservables) || isEmpty(locationsDetails))
		return null;

	const selectedReservables = reservables.filter(r => r.is_selected);
	if (isEmpty(selectedReservables))
		return null;

	let locationDetailsWithBuilding;
	// find first reservable with building
	for (var i = 0; i < selectedReservables.length && !locationDetailsWithBuilding; i++) {
		const srLocationDetails = _getLocationDetailsByEmail(locationsDetails, selectedReservables[i].email);
		if (srLocationDetails && srLocationDetails.building.sys_id)
			locationDetailsWithBuilding = srLocationDetails;
	}

	if (!locationDetailsWithBuilding)
		return null;

	const {
		sys_id: buildingValue,
		display_value: buildingDisplayValue 
	} = locationDetailsWithBuilding.building;

	return {
		building: {
			value: buildingValue,
			displayValue: buildingDisplayValue
		},
		floor: []
	};
}