import { actionTypes } from '@servicenow/ui-core';
import { CONSTANTS } from './constants';
import { CONSTANTS as APP_CONSTANTS } from '../sn-wsd-moa-app/constants';
import { ACTIONS } from './actions';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({state, properties, updateState}) => {
		const {searchRequest: {building, floors, capacity}} = properties;

		updateState({
			...state,
			building,
			floors,
			capacity
		});
	},

	[ACTIONS.RECORD_PICKER_VALUE_CHANGED]: ({action, state, updateState}) => {
		const {value, displayValue, name} = action.payload;
		const newState = {...state};
		newState[name] = {value, displayValue};

		if (name === CONSTANTS.RECORD_PICKER.BUILDING && (!value || value !== state.building.value))
			newState.floors = {value: null, displayValue: []};

		updateState(newState);
	},

	[ACTIONS.NOW_INPUT_CHANGED]: ({action, state, updateState}) => {
		const {payload: {fieldValue, name}} = action;

		updateState({
			...state,
			[name]: fieldValue
		});
	},

	[ACTIONS.ON_SEARCH_BUTTON_CLICKED]: ({state, dispatch}) => {
		const { building, floors, capacity } = state;
		dispatch(ACTIONS.UPDATE_SEARCH_REQUEST, {
			building,
			floors,
			capacity
		});
		dispatch(ACTIONS.TRIGGER_NAVIGATION, { view: APP_CONSTANTS.VIEWS.SEARCH_RESULTS });
	}
};
