import {createHttpEffect} from '@servicenow/ui-effect-http';
import {ACTIONS} from '../actions';

export const searchReservables = createHttpEffect('api/sn_wsd_rsv/search/reservable', {
	queryParams: ['reservable_module', 'start', 'end', 'q', 'reserved_reservables', 'reservation_ids', 'include_standard_services', 'include_reservable_purposes', 'next_item_index', 'page_size'],
	successActionType: ACTIONS.ON_SEARCH_SUCCESS,
	errorActionType: ACTIONS.ON_SEARCH_FAILED,
	batch: false
});