import {CONSTANTS} from '../sn-wsd-moa-app/constants';

function loadJS(url, callback, location) {
	const scriptTag = document.createElement('script');
	scriptTag.src = url;
	scriptTag.onload = callback;
	scriptTag.onreadystatechange = callback;
	location.appendChild(scriptTag);
}

export async function loadOffice() {
	return new Promise((resolve, reject) => {
		loadJS(CONSTANTS.OFFICEJS_URL, () => {
		// This promise is wrapped in a promise because it is called from the onload
		window.Office.onReady()
			.then(resolve)
			.catch(reject);
	}, document.body);
	});
}
