import { t } from 'sn-translate';
import '@servicenow/now-record-field-lookup-connected';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-input';

import * as utils from '../utils/generalUtils';

import { CONSTANTS } from './constants';
import { ACTIONS } from './actions';

export const view = (state, {updateState, dispatch}) => {
	const {building, floors, capacity} = state;

	const isValidCapacity = () => {
		if (!utils.isValidNumber(capacity))
			return false;
		
		if (capacity < 1)
			return false;

		return true;
	}

	return (	
		<div className="sn-wsd-moa-search-filter">
			<sn-wsd-moa-layout>
				<div slot='layoutBody' className="body">
					<now-heading
						className="margin-top-default"
						label={t('Reserve a space')}
						variant="header-tertiary"
						wontWrap={true}
					/>
					<div>
						<div className="input-field">
							<now-record-field-lookup-connected
								label={t('Building')}
								name={CONSTANTS.RECORD_PICKER.BUILDING}
								tableName={CONSTANTS.TABLES.BUILDING}
								fieldName="name"
								value={building.value}
								displayValue={building.displayValue}
								required={true}
							/>
						</div>
						<div className="input-field">
							<now-record-field-lookup-connected
								label={t('Floor')}
								name={CONSTANTS.RECORD_PICKER.FLOOR}
								tableName={CONSTANTS.TABLES.FLOOR}
								fieldName="name"
								additionalQueryString={`building=${building.value}`}
								multiSelection={!!building.value}
								value={floors.value}
								displayValue={floors.displayValue}
								readonly={!building.value}
							/>
						</div>
						<div className="input-field">
							<now-input
								className="full-width"
								label={t('Capacity')}
								name="capacity"
								type="text"
								value={capacity}
								invalid={!isValidCapacity()}>
								<now-button-iconic
									bare
									slot="end"
									icon="minus-fill"
									variant="tertiary"
									disabled={!utils.isValidNumber(capacity)}
									on-click={() => utils.isValidNumber(capacity) && updateState({...state, capacity: Number(capacity) - 1})}
								/>
								<now-button-iconic
									bare
									slot="end"
									icon="plus-fill"
									variant="tertiary"
									disabled={!utils.isValidNumber(capacity)}
									on-click={() => utils.isValidNumber(capacity) && updateState({...state, capacity: Number(capacity) + 1})}
								/>
							</now-input>
						</div>
						<now-button
							className="search-btn"
							label={t('Search')}
							variant="secondary"
							size="md"
							on-click={() => building.value && isValidCapacity() && dispatch(ACTIONS.ON_SEARCH_BUTTON_CLICKED)}
							disabled={!building.value || !isValidCapacity()}
						/>
					</div>
				</div>
			</sn-wsd-moa-layout>
		</div>
	);
}
