import {createHttpEffect} from '@servicenow/ui-effect-http';
import {ACTIONS} from '../actions';

export const getLocationsDetails = createHttpEffect('api/sn_wsd_rsv/reservable/list_reservables/:ids', {
	pathParams: ['ids'],
	queryParams: [
		'reservable_module_sys_id',
		'include_flexible_services',
		'include_standard_services',
		'include_reservable_purposes',
		'include_layouts'
	],
	successActionType: ACTIONS.ON_LOAD_LOCATIONS_DETAILS_SUCCESS,
	errorActionType: ACTIONS.ON_LOAD_LOCATIONS_DETAILS_FAILURE,
	encodeURIComponent: false,
	batch: false
});