import { t } from 'sn-translate';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-text-link';
import '@servicenow/now-button';
import '@servicenow/now-loader';
import '@servicenow/now-heading';
import _isEmpty from 'lodash/isEmpty';

import { CONSTANTS } from '../sn-wsd-moa-app/constants';
import { CONSTANTS as RESERVATION_SUMMARY_CONSTANTS } from './constants';
import { ACTIONS } from './actions';
import * as utils from '../utils/generalUtils';
import '../sn-wsd-moa-layout';
import '../sn-wsd-moa-space-summary-card';
import '../sn-wsd-moa-price-label';

export const view = (...viewInputs) => {
	const [state] = viewInputs;
	switch (state.activeView) {
		case RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_IN_PROGRESS:
			return generateSaveInProgressView(...viewInputs);
		case RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_SUCCESSFUL:
			return generateReservationSavedBody(...viewInputs);
		case RESERVATION_SUMMARY_CONSTANTS.VIEWS.SAVE_FAILED:
			return generateReservationFailedBody(...viewInputs);
		case RESERVATION_SUMMARY_CONSTANTS.VIEWS.RESERVATION_SUMMARY:
			return generateReservationSummary(...viewInputs);
		default:
			return generateReservationSummary(...viewInputs);
	}
}

/**
 * Generates the summary page
 * @param {*} state 
 * @param {*} triggers 
 * @returns {*}
 */
function generateReservationSummary(state, { dispatch }) {
	const {properties: {selectedReservables, locationsServiceCategories, locationsDetails, appointment}, isEligibleForSubmission} = state;

	if (_isEmpty(locationsDetails) && !selectedReservables.some(r => !r.existsInWsd))
		return <div>{t('Preparing meeting...')}</div>

	return (	
		<div className="sn-wsd-moa-reservation-summary" tabindex={0}>
			<sn-wsd-moa-layout>
				<div slot='layoutBody'>
					<div className="header">
						<now-heading
							className="margin-top-default flex-1"
							label={t('Your reservation')}
							variant="header-tertiary"
							wontWrap={true}
						/>
						<now-text-link className="link"
							tabIndex={0}
							hook-insert={vnode => vnode.elm.focus()}
							label={t('Change selections')}
							href="javascript:void(0)"
							on-click={() => { dispatch(ACTIONS.TRIGGER_NAVIGATION, {
								view: CONSTANTS.VIEWS.SEARCH_RESULTS
							})}}
						/>
					</div>
					<div className="body">
						{!utils.isArrayHasElement(selectedReservables) 
							? (<div>{t('No location has been selected!')}</div>) 
							: selectedReservables.map(reservable => (
								<sn-wsd-moa-space-summary-card
									extraServiceRequests={locationsServiceCategories[reservable.sys_id]}
									locationDetails={locationsDetails[reservable.sys_id]}
									reservable={reservable}
									disableServices={appointment.type !== CONSTANTS.RECURRENCE_TYPES.SINGLE}
								/>))
						}
					</div>
				</div>
				<div slot="layoutFooter" className='footer action-buttons'>
					<now-button
						label={ t('Save reservation') }
						size='md'
						variant='primary'
						disabled={!isEligibleForSubmission}
						on-click={ () => dispatch(ACTIONS.SAVE_RESERVATION) } />
				</div>
			</sn-wsd-moa-layout>
		</div>
	);
}

/**
 * Generates the save in progress view
 * @returns {*}
 */
function generateSaveInProgressView() {
	return (
		<div className="sn-wsd-moa-reservation-summary save-progress">
			<div className="sn-wsd-moa-reservation-summary__body center-horizontal-vertical">
				<h1 className="heading">{ t('Booking reservation') }</h1>
				<div className="body-text">
					{ t(`Please don't send the invitation while the reservation is being processed. This may take up to 30 seconds.`) }
				</div>

				<now-loader label={ t('Loading...') } />
			</div>
		</div>
	);
}

/**
 * Generates the view when reservation is sync'd but services might or might not have
 * @param {*} state 
 * @param {*} triggers
 * @returns {*}
 */
function generateReservationSavedBody(...viewInputs) {
	const [ state ] = viewInputs;
	const {
		savedReservables,
		saveServicesFailed,
		properties: {
			locationsServiceCategories,
			locationsDetails
		}
	} = state;

	return (
		<div className="sn-wsd-moa-reservation-summary save-success">
			<sn-wsd-moa-layout>
				<div slot="layoutBody">
					<div className="sn-wsd-moa-reservation-summary__body">
						<h2 className="heading">{ t('Your reservation will be added to your meeting invite.') }</h2>
						<h3 className="heading">{ t('Here are the details of your saved reservation:') }</h3>
						{
							!savedReservables ? '' : savedReservables.map(savedReservable => {
								const locationDetails = locationsDetails[savedReservable.sys_id];
								if (!locationDetails)
									return '';

								const locationServiceCategories = locationsServiceCategories[savedReservable.sys_id];
								return (
									<div className="reservable-card wsd-card">
										<now-heading
											label={savedReservable.name}
											variant="title-primary"
										/>
										<div className="reservable-card__body_text">{ t('Seats {0}', locationDetails.capacity) }</div>
										<div className="reservable-card__body_text">{ utils.constructLocationDescription(locationDetails) }</div>
										{
											!locationServiceCategories ? '' : Object.values(locationServiceCategories).map(serviceCategory => {
												const hasServices = serviceCategory.addedServices.some(addedService => !addedService.cancelFlag);
												if (!hasServices)
													return '';

												const serviceCategoryClassNames = ['reservable-card__service'];
												let serviceCategoryText = '';

												if (!saveServicesFailed)
													serviceCategoryText = t('Added {0}', serviceCategory.categoryFriendlyName.toLowerCase());
												else {
													serviceCategoryClassNames.push('error-text');
													serviceCategoryText = t('Unable to add {0}', serviceCategory.categoryFriendlyName.toLowerCase());
												}
												
												return (
													<div className={serviceCategoryClassNames.join(' ')}>
														{ serviceCategoryText }
													</div>
												);
											})
										}
									</div>
								);
							})
						}
					</div>
				</div>
				<div slot="layoutFooter" className="footer action-buttons">
					<now-button
						label={ t('See details/make changes') }
						size="md"
						on-click={ () => handleEditReservationButtonClicked(true, ...viewInputs) } />
				</div>
			</sn-wsd-moa-layout>
		</div>
	);
}

/**
 * View for when reservation failed to insert/update
 * @param {*} state
 * @param {*} triggers
 * @returns {*}
 */
function generateReservationFailedBody(...viewInputs) {
	return (
		<div className="sn-wsd-moa-reservation-summary save-failed">
			<h1 className="heading">{ t('Unable to create reservation') }</h1>
			
			<div className="sn-wsd-moa-reservation-summary__body">
				{ t('The reservation and extra services could not be requested.') }
			</div>
			<div className="action-buttons">
				<now-button
					label={ t('See details/make changes') }
					on-click={ () => handleEditReservationButtonClicked(false, ...viewInputs) } />
			</div>
		</div>
	);
}

/**
 * Handles when the edit reservation button is clicked on the save completed view
 * @param {boolean} reloadReservation
 * @param {*} state 
 * @param {*} triggers 
 */
function handleEditReservationButtonClicked(reloadReservation, state, { updateState, dispatch }) {
	updateState({...state, activeView: RESERVATION_SUMMARY_CONSTANTS.VIEWS.RESERVATION_SUMMARY });

	if (reloadReservation) // reservation save did not fail 
		dispatch(ACTIONS.TRIGGER_LOAD_RESERVATION);
}