import { ACTIONS } from './actions';
import { actionHandlers } from './actionHandlers';
import { view } from './view';
import styles from './styles.scss';

createCustomElement('sn-wsd-moa-search-filter', {
	renderer: {type: snabbdom},
	actions: ACTIONS,
	actionHandlers: actionHandlers,
	properties: {
		searchRequest: {
			default: {}
		},
		appointment: {
			default: null
		}
	},
	initialState: {
		building: {
			value: null,
			displayValue: null
		},
		floors: {
			value: null,
			displayValue: []
		},
		capacity: 1
	},
	view,
	styles
});
