import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-text-link';
import '@servicenow/now-button';
import '@servicenow/now-heading';
import '@servicenow/now-label-value';
import '@servicenow/now-loader';

import * as utils from '../utils/generalUtils';
import { ACTIONS } from './actions';
import { CONSTANTS as APP_CONSTANTS } from '../sn-wsd-moa-app/constants';
import '../sn-wsd-moa-layout';
import '../sn-wsd-moa-space-card';

export const view = (...viewInputs) => {
	const [state, {dispatch}] = viewInputs;
	const {properties: {reservables, searchRequest: {building, floors, capacity}}} = state;
	const hasSelectedReservables = reservables.some(utils.filterSelectedReservables);
	const floorNames = utils.isArrayHasElement(floors.displayValue) ? floors.displayValue.join(', ') : '';

	return (
		<div className="search-result-wrapper" tabindex={0}>
			<sn-wsd-moa-layout>
				<div slot='layoutBody' className="body">
					<now-heading
						className="margin-top-default"
						label={t('Reserve a space')}
						variant="header-tertiary"
						wontWrap={true}
					/>
					<div className="header">
						<div className="header__title">
							<h3 className="flex-1">
								{t('Showing search results for')}:
							</h3>
							<now-button-bare
								label={t('Edit')}
								variant="primary"
								size="sm"
								hidePadding={true}
								on-click={() => dispatch(ACTIONS.TRIGGER_NAVIGATION, { view: APP_CONSTANTS.VIEWS.SEARCH_FILTER })}
							/>
						</div>
						<div className="search-details">
							<now-label-value-inline
								label={`${t('Location')}:`}
								value={building.displayValue}
							/>
							{!floorNames ? '' : (
								<now-label-value-inline
									label={`${t('Floor')}:`}
									value={floorNames}
								/>
							)}
							<now-label-value-inline
								label={`${t('Capacity')}:`}
								value={capacity}
							/>
						</div>
						<div className="border"></div>
					</div>
					{constructAvailableSpacesView(...viewInputs)}
				</div>
				<div slot="layoutFooter" className='footer action-buttons'>
					<now-button
						label={t('Save and next')}
						variant="secondary"
						size="md"
						disabled={!hasSelectedReservables}
						on-click={() => {
							hasSelectedReservables && dispatch(ACTIONS.ON_SAVE_RESERVABLES)
						}}
					/>
				</div>
			</sn-wsd-moa-layout>
		</div>
	);
}

/**
 * Construct the available spaces view
 * @param {*} state 
 * @param {*} triggers
 * @return {*}
 */
function constructAvailableSpacesView(state, {dispatch}) {
	const { properties: {hasInitSearched, reservables, searchHasMore} } = state;
	
	if (utils.isArrayHasElement(reservables) && hasInitSearched) {
		return (
			<Fragment>
				<now-heading
					label={t('Available spaces')}
					variant="title-primary"
				/>
				{reservables.map(reservable => (
					<sn-wsd-moa-space-card reservable={reservable}/>
				))}
				{ 
					!searchHasMore ? '' :
						<div className="search-results__load-more-button">
							<now-button-bare
								label={t('Load more')}
								on-click={() => dispatch(ACTIONS.TRIGGER_SEARCH)}/>
						</div>
				}
			</Fragment>
		);
	} else if (!utils.isArrayHasElement(reservables) && hasInitSearched) {
		return (
			<p className="hint-text">
				{t('There are no available results for your search.')}
			</p>
		);
	}

	return (
		<p className="hint-text loading">
			<now-loader className="icon" size="md"/>
			{t('Loading reservable information...')}
		</p>
	)
}
