import {createHttpEffect} from '@servicenow/ui-effect-http';
import {ACTIONS} from '../actions';

export const getSettings = createHttpEffect('api/sn_wsd_msaddin/settings/get', {
	successActionType: ACTIONS.ON_LOAD_SETTINGS_SUCCESS,
	errorActionType: ACTIONS.ON_LOAD_SETTINGS_FAILED,
	batch: false
});