import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';

export const view = (state, {updateState}) => {
	const { env, isExpanded, settingsLoaded } = state;

	return (
		<div className="moa-diagnostic-wrapper">
			<div>
				<a className="pull-right"
					href="javascript:void(0)"
					on-click={() => updateState({...state, isExpanded: !state.isExpanded }) }>__</a>
			</div>

			{
				!isExpanded ? '' :
				(
					<Fragment>
						<h5>{t('MOA Add-in information')}</h5>
						<div className="moa-diagnostic__content">
							<table>
								<tr>
									<td>{t('Host')}:</td><td>{env.host}</td>
								</tr>
								<tr>
									<td>{t('Browser')}:</td><td>{env.browser}</td>
								</tr>
								<tr>
									<td>{t('Agent')}:</td><td>{env.agent}</td>
								</tr>
								<tr>
									<td>{t('Req. sets')}:</td><td>{ JSON.stringify(env.supportedVersions) }</td>
								</tr>
								<tr>
									<td colspan="2"><strong>{t('Application info')}</strong></td>
								</tr>
								{
									!env.userConfig ? '' :
									<Fragment>
										<tr>
											<td>{t('Timezone')}:</td><td>{env.userConfig.timezone}</td>
										</tr>
										<tr>
											<td>{t('Date format')}:</td><td>{env.userConfig.date_format}</td>
										</tr>
										<tr>
											<td>{t('Time format')}:</td><td>{env.userConfig.time_format}</td>
										</tr>
									</Fragment>
								}
							</table>

							
						</div>
					</Fragment>
				)
			}
		</div>
	);
};
