export class OfficeRestService {
	DEFAULT_MS_REST_URL = 'https://outlook.office365.com/api';

	/**
	 *
	 * @param {OfficeHelper} officeHelper - instantiated officeHelper
	 */
	constructor(officeHelper) {
		this.officeHelper = officeHelper;
		this.Office = officeHelper.Office;
	}

	/**
	 * Gets all the information about an appointment
	 * @return {Promise<null|any>}
	 */
	async getFullMeetingInfo() {
		const id = await this.officeHelper.getItemId();
		if (!id)
			throw Error('no id found for current appointment');

		const eventId = this.Office.context.mailbox.convertToRestId(
			id,
			this.Office.MailboxEnums.RestVersion.v2_0
		);

		const msRestUrl = this.Office.context.mailbox.restUrl 
			? this.Office.context.mailbox.restUrl
			: this.DEFAULT_MS_REST_URL;

		const url = `${msRestUrl}/v2.0/me/events/${eventId}`;
		console.debug('MOA:: retrieving meeting info', url);
		return this._requestToOffice(url);
	}

	/**
	 * wraps a fetch url with authentication for a request to outlook mailbox rest api
	 * @param {string} url
	 * @return {Promise<any>}
	 * @private
	 */
	async _requestToOffice(url) {
		const accessToken = await this._getAccessToken();
		const response = await fetch(url, {
			method: 'get',
			headers: new Headers({
				'Authorization': `Bearer ${accessToken}`,
				'Content-Type': 'application/json'
			}),
		});
		return response.json();
	}

	/**
	 * gets an access token to get data from microsoft rest api
	 * https://docs.microsoft.com/en-us/office/dev/add-ins/outlook/use-rest-api
	 * @private
	 */
	async _getAccessToken() {
		return new Promise((resolve, reject) => {
			this.Office.context.mailbox.getCallbackTokenAsync({isRest: false}, function (result) {
				if (result.status === "succeeded") {
					var accessToken = result.value;
					resolve(accessToken);
				} else {
					reject(result);
				}
			});
		});
	}

}
