import { actionTypes } from '@servicenow/ui-core';
import { getOfficeHelper } from '../office/officeHelper';

const {COMPONENT_BOOTSTRAPPED} = actionTypes;

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({state, properties, updateState}) => {
		const officeHelper = getOfficeHelper();
		const { userConfig } = properties;

		const env = {
			host: window.location.href,
			browser: _getBrowser(),
			agent: _getFriendlyUseragent(),
			supportedVersions: officeHelper.supportedVersions(),
			userConfig: userConfig
		};

		updateState({
			env
		});
	},
};

/**
 * util function to get browser that the app runs on
 * @return {string} browser 
 */
function _getBrowser() {
	const isIE = /*@cc_on!@*/false || !!document.documentMode;
	const isEdge = !isIE && !!window.StyleMedia;

	if(navigator.userAgent.indexOf('Chrome') != -1 && !isEdge)
		return 'chrome';
	
	if(navigator.userAgent.indexOf('Safari') != -1 && !isEdge)
		return 'safari';

	if(navigator.userAgent.indexOf('Firefox') != -1 ) 
		return 'firefox';
	
	if((navigator.userAgent.indexOf('MSIE') != -1 ) || (!!document.documentMode == true )) //IF IE > 10
		return 'ie';

	if(isEdge)
		return 'edge';
	
	return 'other-browser';
}

/**
 * this function returns the browser and version in a friendly way instead of showing the full user agent
 * @return {string} user friendly client agent
 */
function _getFriendlyUseragent() {
	const userAgent = navigator.userAgent;
	let temp;
	let match = userAgent.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];

	if (/trident/i.test(match[1])) {
		temp =  /\brv[ :]+(\d+)/g.exec(userAgent) || [];
		return 'IE ' + (temp[1] || '');
	}
	if (match[1] === 'Chrome') {
		temp = userAgent.match(/\b(OPR|Edge)\/(\d+)/);
		if (temp != null) { return temp.slice(1).join(' ').replace('OPR', 'Opera'); }
	}
	match = match[2] ? [match[1], match[2]] : [navigator.appName, navigator.appVersion, '-?'];
	if ((temp = userAgent.match(/version\/(\d+)/i)) != null) { match.splice(1, 1, temp[1]); }
	return match.join(' ');
}