import { actionTypes } from '@servicenow/ui-core';

import { ACTIONS } from './actions'

const {COMPONENT_BOOTSTRAPPED, COMPONENT_DOM_READY} = actionTypes;;

export const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch }) => {
		dispatch(ACTIONS.TRIGGER_SEARCH, {reset: true});
	},

	[COMPONENT_DOM_READY]: ({action}) => {
		const {payload: {host}} = action;
		const resultWrapper = host.shadowRoot.querySelector('.search-result-wrapper');
		
		if (resultWrapper)
			resultWrapper.focus();
	}
};