import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import { view } from './view';
import styles from './styles.scss';

createCustomElement('sn-wsd-moa-service-request-card', {
	renderer: {type: snabbdom},
	view,
	properties: {
		serviceRequest: {
			default: null
		},
		subCategory: {
			default: null
		},
		deliveryTime: {
			default: ''
		},
		userConfig: {
			default: null
		}
	},
	initialState: {
		commentOpen: false
	},
	styles
});
